import types from './types';
import get from 'lodash.get';
import {
  modes,
  layoutModes,
  StreamingStates,
  ConnectionStates,
  lectureLayouts
} from '../constants';
import update from 'immutability-helper';
import omit from 'lodash.omit';
import { streamRemoved, statsUpdate, statsAllUpdate } from './actionHandlers';

const subtitlesDistribution = (state, payload, id, translations) => {
  const { subtitles, participantId: uid } = payload;
  const { remoteParticipants, participant: { name } } = state;
  let speaker = '';
  let result = {};
  if (uid === id) {
    result[uid] = { subtitles, name };
    speaker = name;
  } else {
    const speakerName = remoteParticipants[uid] && remoteParticipants[uid].name;
    result[uid] = { subtitles, name: speakerName };
    speaker = speakerName;
  }
  return { subtitles, name: speaker };
  // return result;
};

const getRoomGeometry = (room) => {
  let geometry;
  if (room) {
    const { optimized_layout_full_quality, optimized_layout_threshold } = room;
    geometry = Object.assign({}, lectureLayouts['default'].geometry, {
      optimized_layout_full_quality,
      optimized_layout_threshold
    });
  } else {
    geometry = Object.assign({}, lectureLayouts['default'].geometry);
  }
  const result = {
    optimizedLayoutFullQuality: geometry.optimized_layout_full_quality,
    optimizedLayoutThreshold: geometry.optimized_layout_threshold
  };
  return result;
};

const transcriptChatMessage = (state, { authorId, result, time }) => {
  const { remoteParticipants, participant, subtitlesLangs } = state;
  const author = participant && participant._id === authorId ? participant : remoteParticipants[authorId];

  const data = {
    author,
    message: result,
    time,
    translations: ''
  };

  return data;
};

/** @type MeetingRxState */
const initialState = {
  participantToken: '',
  participant: null,
  remoteParticipants: {},
  roomOptions: {
    enableChat: false,
    hasAccessPin: false,
    hasOwnerPin: false
  },
  viewersRequests: [],
  layoutGeometry: getRoomGeometry(),
  room: null,
  connectionState: ConnectionStates.DISCONNECTED,
  requiresAccessPin: false,
  connection: null,
  streamStats: {},
  moderationSocket: null,
  sipCallState: { state: 0, sipUri: '' },
  /// set these after connection
  mode: modes.INTERACTIVE,
  streams: {},
  meetingId: '',
  streamingState: StreamingStates.STOPPED,
  isConnected: false, // xxx
  startTime: 0,
  slideNumber: 0,
  storyboardId: null,
  chatLog: [],
  lecturerId: null,
  transcript: [],
  subtitles: [],
  subtitlesLangs: [],
  selectedForLecture: [],
  lectureLayout: 0,
  layoutMode: 'auto',
  isPresenter: false,
  bfmPaused: false
};

const fillLayout = (layout, layoutLength, lecturerId) => {
  let streamList = layout.slice();
  if (streamList.indexOf(lecturerId) === -1) {
    streamList = [lecturerId, ...streamList];
  }
  if (streamList.length === layoutLength) return streamList;
  if (streamList.length > layoutLength) {
    streamList = [lecturerId, ...streamList.filter(val => ['stub', lecturerId]
      .indexOf(
        val) === -1)];
    streamList = streamList.slice(0, layoutLength);
  }
  for (let i = 0; i < layoutLength; i++) {
    const item = streamList[i];
    if (!item) streamList[i] = 'stub';
  }
  return streamList;
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case types.SET_SIP_STATE:
      return update(state, {
        sipCallState: { $set: payload }
      });
    case types.STATS_PARTICIPANT:
      return statsUpdate(state, payload);
    case types.SCREEN_SHARE_SET_REMOTE:
      return update(state, {
        remoteParticipants: {
          [payload.participantId]: {
            streams: { screenStreamId: { $set: payload.streamId } }
          }
        }
      });
    case types.SCREEN_SHARE_STARTED:
      return update(
        state, { participant: { streams: { screenStreamId: { $set: payload } } } });
    case types.SET_STREAMING_STATE:
      return Object.assign({}, state, { streamingState: payload });
    // case types.CONNECTION_CANCEL:
    // return Object.assign({}, state, { requiresAccessPin: false });
    case types.SET_ACCESS_PIN_REQ:
      return Object.assign({}, state, { requiresAccessPin: payload });
    case types.SET_CONNECTION_STATE:
      return Object.assign({}, state, { connectionState: payload });
    case types.STATS_ROOM:
      return statsAllUpdate(state, payload); //Object.assign({}, state, { streamStats: payload });
    case types.CONNECTION_PARTICIPANT_REMOVED:
      return update(state, {
        remoteParticipants: { $unset: [payload] }
      });
    case types.CONNECTION_STREAM_REMOVED:
      return streamRemoved(state, payload);
    case types.CONNECTION_STREAM_SUBSCRIBED:
      return update(state, {
        streams: {
          [payload.getID()]: { $set: payload }
        }
      });
    case types.CONNECTION_ADD_PARTICIPANT:
      return update(state, {
        remoteParticipants: {
          [payload.participant._id]: { $set: payload.participant }
        }
      });
    case types.CONNECTION_ADD_PARTICIPANT_STREAM:
      return update(state, {
        remoteParticipants: {
          [payload.participantId]: {
            streams: { mediaStreamId: { $set: payload.streamId } }
          }
        }
      });
    case types.SET_MEETING_STATE:
      localStorage.setItem('partName', payload.participants[state.participant._id].name);
      localStorage.setItem('roomId', state.room.id);
      return Object.assign({}, state, payload.state, {
        remoteParticipants: omit(payload.participants, state.participant._id),
        participant: Object.assign({},
          state.participant,
          payload.participants[state.participant._id]),
        roomOptions: Object.assign({}, state.roomOptions, payload.state
          .roomOptions)
      });
    case types.MEETING_CHANGE_MODE:
      return update(state, {
        $merge: payload.state
      });
    case types.MEETING_CHANGE_LAYOUT:
      return update(state, {
        $merge: payload
      });
    case types.ROOM_READY:
      return Object.assign({}, state, {
        isConnected: true,
        connectionState: ConnectionStates.CONNECTING
      });
    case types.LOCAL_STREAM_ADDED:
      let id = null;
      if (payload) id = payload.getID();
      return update(state, {
        localStreamId: { $set: id },
        participant: { streams: { mediaStreamId: { $set: id } } },
        streams: {
          [id]: { $set: payload }
        }
      });
    case types.MODERATION_SOCKET_CREATED:
      return Object.assign({}, state, { moderationSocket: payload });
    case types.CREATE_CONNECTION_SUCCESS:
      return Object.assign({}, state, {
        connection: payload.connection,
        connectionState: ConnectionStates.CONNECTING
      });

    case types.MEETING_SET_MODE:
      // used only for change mode by UI
      return Object.assign({}, state, {
        mode: payload.mode,
        storyboardId: payload.stbId,
        layoutGeometry: payload.mode === modes.INTERACTIVE ?
          getRoomGeometry(state.room) : state.layoutGeometry,
        lecturerId: payload.mode === modes.INTERACTIVE ?
          null :
          state.participant ? state.participant._id : null // null when we start storyboard from admin panel
      });

    case types.SET_LECTURE_LAYOUT:
      return Object.assign({}, state, {
        lectureLayout: payload.lectureLayout
      });
    case types.MEETING_LAYOUT_MODE:
      if (payload.layoutMode === layoutModes.AUTO) {
        return Object.assign({}, state, {
          layoutMode: payload.layoutMode,
          lectureLayout: 0
        });
      } else {
        return Object.assign({}, state, {
          layoutMode: payload.layoutMode
        });
      }
    case types.MEETING_SLIDE_NUMBER:
      return Object.assign({}, state, {
        slideNumber: payload.number
      });

    case types.MEETING_SLIDE_CHANGED:
      return Object.assign({}, state, {
        slideNumber: payload.number
      });
    case types.UPDATE_LAYOUT_GEOMETRY:
      return Object.assign({}, state, {
        layoutGeometry: payload.geometry
      });
    case types.UPDATE_LAYOUT_PARTICIPANTS:
      const len = state.layoutGeometry.optimizedLayoutFullQuality + state
        .layoutGeometry
        .optimizedLayoutThreshold;
      const lecturerId = state.lecturerId
        ? state.lecturerId
        : state.participant._id;
      return Object.assign({}, state, {
        selectedForLecture: fillLayout(
          payload.selectedForLecture || state.selectedForLecture,
          len,
          lecturerId)
      });

    case types.UPDATE_PANEL_LAYOUT_PARTICIPANTS:
      return Object.assign({}, state, {
        selectedForLecture: payload.selectedForLecture
      });

    case types.MUTE_UPDATED:
      let { participantId, mute } = payload;
      return Object.assign({}, update(state, {
        remoteParticipants: {
          [participantId]: { status: { mute: { $set: mute } } }
        }
      }));

    case types.UNMUTE_REQUEST_SENT:
      const remotePart = state.remoteParticipants[payload.participantId];
      if (!remotePart) return state;
      if (remotePart.unmuteRequest) {
        return Object.assign({}, update(state, {
          remoteParticipants: {
            [payload.participantId]: {
              status: {
                unmuteRequest: {
                  $merge: {
                    [payload.device]: true
                  }
                }
              }
            }
          }
        }));
      } else {
        return Object.assign({}, update(state, {
          remoteParticipants: {
            [payload.participantId]: {
              status: {
                $merge: {
                  unmuteRequest: Object.assign({}, remotePart.unmuteRequest, { [payload.device]: true })
                }
              }
            }
          }
        }));
      }
    case types.MODERATOR_UNMUTE_REQUEST:
      if (state.participant.status.unmuteRequest) {
        return Object.assign({}, update(state, {
          participant: {
            status: {
              unmuteRequest: {
                $merge: {
                  [payload.device]: true
                }
              }
            }
          }
        }));
      } else {
        return Object.assign({}, update(state, {
          participant: {
            status: {
              $merge: {
                unmuteRequest: Object.assign({}, state.participant.status.unmuteRequest, { [payload.device]: true })
              }
            }
          }
        }));
      }
    case types.MODERATOR_UNMUTE_RESPONSE:
      const newRequestState = {};
      if (payload.device.hasOwnProperty('audio')) newRequestState.audio = false;
      if (payload.device.hasOwnProperty('video')) newRequestState.video = false;
      return Object.assign({}, update(state, {
        remoteParticipants: {
          [payload.senderId]: {
            status: {
              unmuteRequest: { $merge: newRequestState }
            }
          }
        }
      }));
    case types.UNMUTE_RESPONSE:
      if (state.participant.status.unmuteRequest) {
        return Object.assign({}, update(state, {
          participant: {
            status: {
              unmuteRequest: {
                $merge: {
                  [payload.device]: false
                }
              }
            }
          }
        }));
      } else {
        return Object.assign({}, update(state, {
          participant: {
            status: {
              $merge: {
                unmuteRequest: Object.assign(({}, state.participant.status.unmuteRequest), {
                  [payload.device]: false
                })
              }
            }
          }
        }));
      }
    case types.SET_MUTE_STATE:
      return Object.assign({}, update(state, {
        participant: {
          status: {
            $set: Object.assign({}, state.participant
              .status, { mute: payload })
          }
        }
      }));
    case types.STATUS_UPDATE:
      if (state.participant.status) {
        return update(state, {
          participant: { status: { $merge: payload } }
        });
      } else {
        return Object.assign({}, state, {
          participant: Object.assign({},
            state.participant, { status: payload }
          )
        });
      }

    case types.REQUEST_OWNER_PIN:
      return Object.assign({}, state, { requestOwnerPinAction: payload.action });

    case types.OWNER_PIN_SUCCESS:
    case types.ROLE_CHANGED:
      if (state.participant._id === payload.participantId) {
        return update(state, {
          participant: { $merge: payload.data }
        });
      } else {
        return Object.assign({}, update(state, {
          remoteParticipants: {
            [payload.participantId]: { $merge: payload.data }
          }
        }));
      }
    case types.PARTICIPANT_NAME_CHANGED:
      if (state.participant._id === payload.participantId) {
        return update(state, {
          participant: {
            name: { $set: payload.name }
          }
        });
      } else {
        return Object.assign({}, update(state, {
          remoteParticipants: {
            [payload.participantId]: {
              name: { $set: payload.name }
            }
          }
        }));
      }
    case types.PARTICIPANT_AVATAR_SET:
      if (state.participant._id === payload.participantId) {
        return update(state, {
          participant: {
            avatar: { $set: payload.avatar }
          }
        });
      } else {
        return Object.assign({}, update(state, {
          remoteParticipants: {
            [payload.participantId]: { avatar: { $set: payload.avatar } }
          }
        }));
      }

    case types.SET_CURRENT_ROOM:
      return Object.assign({}, state, {
        room: payload
      });
    case types.CREATE_PARTICIPANT_SUCCESS:
      return Object.assign({}, state, {
        participant: payload,
        participantToken: get(payload, 'token', '')
      });
    case types.CONNECTION_DISCONNECTED:
      return Object.assign({}, state, initialState);
    case types.CREATE_PARTICIPANT_ERROR:
      return Object.assign({}, state, {
        connectionState: payload.state
      });
    case types.ADD_CHAT_MESSAGE:
    case types.SEND_MESSAGE:
      return Object.assign({}, state, {
        chatLog: [].concat(state.chatLog, payload)
      });
    case types.ADD_TRANSCRIPT_MESSAGE:
    case types.SEND_TRANSCRIPT:
      // transcript's chat messages 
      const message = transcriptChatMessage(state, payload);
      return Object.assign({}, state, {
        transcript: [].concat(state.transcript, message)
      });
    case types.SHARE_TRANSCRIPT: // my
    case types.RECEIVE_TRANSCRIPT: // user's
      return _recvTranscript(state, payload);
    case types.RECEIVE_SUBTITLES_REQUEST:
    case types.TURN_ON_SUBTITLES: {
      if (!state.subtitlesLangs.includes(payload.value))
        return Object.assign({}, state, {
          subtitlesLangs: [].concat(state.subtitlesLangs, payload.value)
        });
      else
        return state;
    }
    case types.RAISE_HAND_REQ:
      return _rhRequest(state, payload);
    case types.RAISE_HAND_UPDATE:
      return _rhUpdate(state, payload);
    case types.RECEIVED_REQUEST_TO_PARTICIPATE:
      return _recvReqToParticipate(state, payload);
    case types.SET_BFM_PAUSE_STATE:
    case types.BFM_ON_PAUSE:
      return Object.assign({}, state, { bfmPaused: payload });
    default:
      return state;
  }
};

const _recvTranscript = (state, payload) => {
  const id = get(state, 'participant._id');
  if (id) {
    const subtitles = subtitlesDistribution(state, payload, id);
    return Object.assign({}, state, {
      subtitles: [].concat(state.subtitles, subtitles)
    });
  }
  return state;
};

const _recvReqToParticipate = (state, payload) => {
  const { username, question, id } = payload.data.params;
  const { participant: { role, isRoomOwner } } = state;
  if (role === 'Moderator' || isRoomOwner) {
    const time = new Date().getTime();
    const viewersRequests = [].concat(state.viewersRequests, { username, question, time, id });
    return Object.assign({}, state, { viewersRequests });
  }
  return state;
};

const _rhRequest = (state, payload) => {
  const newState = Object.assign({}, state, {
    participant: Object.assign({}, state.participant, {
      status: Object.assign({}, state.participant.status, {
        handRaised: payload
      })
    })
  });
  return newState;
};

const _rhUpdate = (state, payload) => {
  const { participantId, state: isRaised } = payload;
  let newState = Object.assign({}, state, {
    remoteParticipants: Object.assign({}, state.remoteParticipants, {
      [participantId]:
        Object.assign({}, state.remoteParticipants[participantId], {
          status: Object.assign({}, state.remoteParticipants[participantId].status, {
            handRaised: isRaised
          })
        })
    })
  });
  return newState;
};