import get from 'lodash.get';
import update from 'immutability-helper';
export const streamRemoved = (state, payload) => {
  const streamId = payload.getID();
  const query = {
    streams: {
      $unset: [streamId]
    }
  };
  const pStreamId = get(state, 'participant.streams.mediaStreamId');
  if (streamId === pStreamId) {
    query.participant = {
      streams: {
        mediaStreamId: { $set: null }
      }
    };
  } else {
    for (const pId in state.remoteParticipants) {
      if (state.remoteParticipants.hasOwnProperty(pId)) {
        const sId = get(state, `remoteParticipants.${pId}.streams.mediaStreamId`, '');
        if (sId === streamId) {
          query.remoteParticipants = {
            [pId]: {
              streams: {
                mediaStreamId: { $set: null }
              }
            }
          };
          break;
        }
      }
    }
  }

  return update(state, query);
};

export const statsUpdate = (state, stats) => {
  const query = { streamStats: {} };
  try {
    for (const pId in stats) {
      if (stats.hasOwnProperty(pId)) {
        query.streamStats[pId] = {
          $push: [{
            value: get(stats, `${pId}.qoe`, 0),
            name: 'stats-qoe',
            label: 'QOE'
          }]
        };
      }
    }
    return update(state, query);
  } catch (error) {
    return state;
  }
};

export const statsAllUpdate = (state, stats) => {
  const query = { streamStats: {} };
  try {
    for (const pId in stats) {
      if (stats.hasOwnProperty(pId)) {
        const [qoe] = get(state, `streamStats.${pId}`, []).filter(s => s.name === 'stats-qoe');
        if (qoe) {
          query.streamStats[pId] = {
            $set: [].concat(stats[pId], qoe)
          };
        } else {
          query.streamStats[pId] = {
            $set: stats[pId]
          };
        }
      }
    }
    return update(state, query);
  } catch (error) {
    return state;
  }

};