import types from './types';

export default Object.freeze({
  requestToParticipate: data => ({ type: types.REQUEST_TO_PARTICIPATE, payload: data }),
  viewerLeavesPlaybackPage: data => ({ type: types.VIEWER_LEAVES_PLAYBACK_PAGE, payload: data }),
  acceptRequestToParticipate:     id => ({ type: types.ACCEPT_REQUEST_TO_PARTICIPATE, payload: id }),
  requestToParticipateAccepted: data => ({ type: types.REQUEST_TO_PARTICIPATE_ACCEPTED, payload: data }),
  rejectRequestToParticipate:    id => ({ type: types.REJECT_REQUEST_TO_PARTICIPATE, payload: id }),
  requestToParticipateRejected: data => ({ type: types.REQUEST_TO_PARTICIPATE_REJECTED, payload: data }),
  noop: () => ({ type: types.NOOP }),
  closeViewerChannel: data => ({ type: types.CLOSE_VIEWER_CHANNEL, payload: data }),
  openViewerChannel: (data) => ({ type: types.OPEN_VIEWER_CHANNEL, payload: data }) ,
  viewerChannelOpened: socket => ({ type: types.VIEWER_CHANNEL_OPENED, payload: socket }),
  viewerChannelClosed: () => ({ type: types.VIEWER_CHANNEL_CLOSED }),
  requestToParticipateSent: data => ({ type: types.REQUEST_TO_PARTICIPATE_SENT, payload: data }),
  clearRequestData: () => ({ type: types.CLEAR_REQUEST_DATA }),
  joinToMeeting: () => ({ type: types.JOIN_TO_MEETING }),
  receiveStreamingStatus: data => ({ type: types.RECEIVE_STREAMING_STATUS, payload: data }),
  setMeetingState: (meetingState) => ({
    type: types.SET_MEETING_STATE,
    payload: meetingState
  }),
  setReceivedStreamingState: state => ({ type: types.SET_RECEIVED_STREAMING_STATE, payload: state })
});