import get from 'lodash.get';
import {
  put,
  select
} from 'redux-saga/effects';
import {
  participantSelector,
  remoteParticipantsSelector,
  streamsSelector
} from '../selectors';
import actions from '../actions';
import streamStatsFirefox from './firefox';
import streamStatsChrome from './chrome';
import { mSocketSend } from '../../utils';
import moderationActions from '../../moderationActions';

/**
 * Stream statistics little animal
 *
 * @private
 * @param {Stream} stream
 * @prop {Any} _instances
 */
export default function streamStats(stream) {
  if (!streamStats._instances)
    streamStats._instances = {};
  if (!stream)
    return Promise.reject('stream is null');
  let instance = streamStats._instances[stream.getID()];
  if (!instance) {
    if (window.chrome)
      instance = new streamStatsChrome(stream);
    else
      instance = new streamStatsFirefox(stream);
    streamStats._instances[stream.getID()] = instance;
  } else {
    if (!instance.stream.pc) instance.stream = stream;
  }
  return instance.compute();
}

export function* statsMonitor() {
  const participants = yield select(remoteParticipantsSelector);
  const streams = yield select(streamsSelector);
  const participant = yield select(participantSelector);
  const stats = {};
  if (get(participant, 'streams.mediaStreamId')) {
    const streamId = participant.streams.mediaStreamId;
    const stream = streams[streamId];
    if (stream) {
      stats[participant._id] = yield streamStats(stream)
        .catch(function (err) {
          console.log('# err', err);
          return [];
        });
    } else {
      stats[participant._id] = [];
    }
    const statsToSend = {
      qoe: 0,
      videoResolution: 0,
      bandwidth: 0
    };
    stats[participant._id].forEach(stat => {
      switch (stat.name) {
        case 'stats-qoe':
          statsToSend.qoe = stat.value;
          break;
        case 'stats-video-resolution':
          statsToSend.videoResolution = stat.value;
          break;
        case 'stats-video-bw':
          statsToSend.bandwidth = stat.value;
          break;
        case 'stats-audio-codec':
          statsToSend.audioCodec = stat.value;
          break;
        case 'stats-video-codec':
          statsToSend.videoCodec = stat.value;
          break;
        default:
          break;
      }
    });
    yield mSocketSend(moderationActions.stats, statsToSend);
  }
  for (const pId in participants) {
    const streamId = get(participants, `${pId}.streams.mediaStreamId`);
    if (participants.hasOwnProperty(pId) && streamId) {
      const stream = streams[streamId];
      if (stream) {
        stats[pId] = yield streamStats(stream)
          .catch(function (err) {
            console.log('# err', err);
            return [];
          });
      } else {
        stats[pId] = [];
      }
    }
  }
  yield put(actions.roomStats(stats));
}
