import { select } from 'redux-saga/effects';
import {
  participantCallOptionsSelector,
  userCallOptionsSelector,
  socketSelector,
  tokenSelector
} from './meeting/selectors';

export function* mSocketSend(moderationAction, ...args) {
  /** @type {WebSocket} */
  const mSocket = yield select(socketSelector);
  const token = yield select(tokenSelector);
  if (mSocket && mSocket.readyState === 1)
    mSocket.send(JSON.stringify(moderationAction(token, ...args)));
}

export const apiCallHelper = (serverUrl, accessToken, method, url, data) => {
  const options = {
    method: method.toUpperCase(),
    headers: { 'Accept': 'application/json', 'cache-control': 'no-store' }
  };

  if (url === '/accesstokens') {
    const bAuthString = window.btoa(`${data.username}:${data.password}`);
    options.headers['Authorization'] = `Basic ${bAuthString}`;
  } else if (accessToken)
    options.headers['Authorization'] = `Bearer ${accessToken}`;

  if (['post', 'put', 'patch'].indexOf(method.toLowerCase()) !== -1 && data) {
    options.body = JSON.stringify(data);
    options.headers['Content-Type'] = 'application/json';
  }

  return fetch(`https://${serverUrl}/api/v1${url}`, options)
    .then((response) => {
      if (response.status === 204) return {};
      const parsed = response.json().catch((error) => {
        console.error(`Error calling ${url}: ${error}`);
        return {};
      });
      if (response.status >= 200 && response.status < 300) {
        let token = accessToken;
        if (response.headers.get('Authorization'))
          token = response.headers.get('Authorization').replace(/^Bearer /, '');
        return parsed.then(res => ({ payload: res, token }));
      }
      if (response.status === 401) {
        return parsed.then((res) => {
          const error = {
            code: response.status,
            type: res.type || res.name || '',
            message: res.message || res.type,
            details: res.details || ''
          };
          const message_split = res.message.split('\n');
          if (message_split.length > 1) {
            res.details = res.details || res.message;
            res.message = message_split[0];
          }
          throw error;
        });
      } else {
        return parsed.then((res) => {
          const message = res.message || response.statusText;
          const error = { code: response.status, message };
          throw error;
        });
      }
    });
};

export function* callApiAsUser(method, url, data) {
  const { token, serverUrl } = yield select(userCallOptionsSelector);
  const response = yield apiCallHelper(serverUrl, token,
    method, url, data);
  return response;
}

export function* callApiAsParticipant(method, url, data) {
  const { token, serverUrl } = yield select(participantCallOptionsSelector);
  const { payload } = yield apiCallHelper(serverUrl, token, method, url, data);
  return payload;
}

/**
 * Re-brands the stream so to have the TeeVid logo.
 *
 * @private
 * @param {Stream} stream
 */
export function rebrandStream(stream) {
  var _playFn = stream.play;
  stream.play = function () {
    if (!stream.hasAudio() && !stream.hasVideo() && !stream.hasScreen()) {
      console.warn('Stream', stream.getID(), 'does not have media channels');
      return;
    }
    _playFn.apply(stream, arguments);
    if (stream.player.bar) {
      stream.player.bar.link.setAttribute('href', 'http://teevid.com/');
      stream.player.bar.logo.setAttribute('alt', '');
      stream.player.bar.logo.setAttribute('src', 'assets/cayilogo.png');
    }
  };
  stream.show = stream.play;
  stream.isPublished = function () {
    // id is local when video is not published yet
    return stream.stream && stream.getID() && stream.getID() !== 'local';
  };
}

export const generateThumbnail = (stream) => {
  if (!stream || !stream.hasVideo() || stream.videoMuted)
    return null;
  try {

    // Create Image from snapshot
    let tmpCanvas = document.createElement('canvas');
    let tmpContext = tmpCanvas.getContext('2d');
    let bitmap = stream.getVideoFrame();
    if (!bitmap)
      return null;
    let brokenImage = true;
    for (let i = 0; i < 20; i++) {
      if (bitmap.data[i] !== 0) brokenImage = false;
    }
    if (brokenImage)
      return null;

    tmpCanvas.width = bitmap.width;
    tmpCanvas.height = bitmap.height;
    tmpContext.putImageData(bitmap, 0, 0);

    // Need to make the bitmap square
    let thumbCanvas = document.createElement('canvas');
    const thumbContext = thumbCanvas.getContext('2d');

    const { width, height } = bitmap;
    if (width > height) {
      thumbCanvas.width = height;
      thumbCanvas.height = height;
      thumbContext.drawImage(tmpCanvas, (width - height) / 2, 0, height, height, 0, 0, height, height);
    } else {
      thumbCanvas.width = width;
      thumbCanvas.height = width;
      thumbContext.drawImage(tmpCanvas, 0, (height - width) / 2, width, width, 0, 0, width, width);
    }
    return thumbCanvas.toDataURL('image/png');
  } catch (e) {
    console.warn('Thumbnail exception %s for stream %o', e, stream);
  }
  return null;
};

export const parseJwt = token => JSON.parse(atob(token.split('.')[1]));
