export const hasOwnerPinSelector = (state) => state.teevid.meeting.roomOptions.hasOwnerPin;

export const participantStreamSelector = (state, participantId) => {
  let participant;
  if (state.teevid.meeting.participant._id === participantId)
    participant = state.teevid.meeting.participant;
  else
    participant = state.teevid.meeting.remoteParticipants[participantId];

  if (participant && participant.streams && participant.streams.mediaStreamId)
    if (state.teevid.meeting.streams && state.teevid.meeting.streams[participant.streams.mediaStreamId])
      return state.teevid.meeting.streams[participant.streams.mediaStreamId];

  return null;
};

export const canModerateSelector = (state) => {
  return [roles.MODERATOR, roles.OWNER].indexOf(state.teevid.meeting.participant.role)
    !== -1;
};

export const isOwnerSelector = (state) => {
  return state.teevid.meeting.participant.role === roles.OWNER;
};

export const participantCallOptionsSelector = (state) => ({
  token: state.teevid.meeting.participantToken,
  serverUrl: state.teevid.user.serverUrl,
  mSocket: state.teevid.meeting.moderationSocket
});

export const chatLogSelector = state => state.teevid.meeting.chatLog;
