import apiReducer from './api/reducer';
import apiSaga from './api/saga';
import apiActions from './api/actions';
import apiTypes from './api/types';

import userReducer from './user/reducer';
import userSaga from './user/saga';
import userActions from './user/actions';
import userTypes from './user/types';

import viewerReducer from './viewer/reducer';
import viewerSaga from './viewer/saga';
import viewerActions from './viewer/actions';
import viewerTypes from './viewer/types';

import meetingReducer from './meeting/reducer';
import meetingSaga from './meeting/saga';
import meetingActions from './meeting/actions';
import meetingTypes, { roles } from './meeting/types';
import { bindActionCreators, combineReducers } from 'redux';
import get from 'lodash.get';
import { all } from 'redux-saga/effects';
import Erizo from './libs/erizo';
import { rebrandStream, apiCallHelper, callApiAsParticipant, parseJwt } from './utils';
import mActions from './moderationActions';
import { ConnectionStates } from './constants';

export const actionTypes = Object.assign({}, apiTypes, userTypes, meetingTypes, viewerTypes);
export const actions = Object.assign({}, apiActions, userActions, meetingActions, viewerActions);
export const moderationActions = mActions;

class TeeVid {
  constructor(logLevel = 3) {
    Erizo.Logger.setLogLevel(logLevel);
    window.TeeVidSdk = this;
  }
  getStream(participantId) {
    const { remoteParticipants, streams } = this.store.getState().teevid.meeting;
    const streamId = get(remoteParticipants, `${participantId}.streams.mediaStreamId`, null);
    if (streamId) return get(streams, streamId, null);
    return null;
  }
  get localStream() {
    const { participant, streams, connectionState } = this.store.getState().teevid.meeting;
    if (connectionState === ConnectionStates.DISCONNECTED) return null;
    const streamId = get(participant, 'streams.mediaStreamId', null);
    if (streamId) return get(streams, streamId, null);
    return null;
  }

  get presenterStream() {
    const {
      lecturerId,
      participant,
      streams,
      remoteParticipants,
      connectionState } = this.store.getState().teevid.meeting;
    if (connectionState === ConnectionStates.DISCONNECTED) return null;
    if (!lecturerId) return null;
    if (participant && (participant._id === lecturerId)) return this.localStream;
    const lecturer = remoteParticipants[lecturerId];
    if (lecturer) {
      const streamId = get(lecturer, 'streams.mediaStreamId', null);
      if (streamId) return get(streams, streamId, null);
    }
    return null;
  }

  get actionTypes() {
    return actionTypes;
  }
  get logo() {
    const { serverUrl } = this.store.getState().teevid.user;
    return `https://${serverUrl}/api/v1/system/logo`;
  }
  get reducer() {
    return combineReducers({
      api: apiReducer,
      user: userReducer,
      meeting: meetingReducer,
      viewer: viewerReducer
    });
  };
  get saga() {
    return function* TeeVidSaga() {
      yield all([
        apiSaga(),
        userSaga(),
        meetingSaga(),
        viewerSaga()
      ]);
    };
  };
  get profile() {
    if (this.store) {
      const state = this.store.getState();
      return get(state, 'teevid.user.profile', null);
    }
    return null;
  }

  get userToken() {
    if (this.store) {
      const state = this.store.getState();
      const token = get(state, 'teevid.user.userToken', null);
      return token;
    }
    return null;
  }

  get remoteStreams() {
    const connection = this.store.getState().teevid.meeting.connection;
    const streams = {};
    if (connection && connection.remoteStreams) {
      connection.remoteStreams.forEach(stream => {
        if (!stream.local) streams[stream.getID()] = stream;
      });
    }
    return streams;
  }

  sendModerationMessage(message) {
    const state = this.store.getState();
    const mSocket = get(state, 'teevid.meeting.moderationSocket');
    const token = get(state, 'teevid.meeting.participantToken');
    // check if socket is there and is not closed
    if (!mSocket || !token || (mSocket.readyState !== 1))
      throw new Error({ message: 'error-connect-socket' });
    message.token = token;
    mSocket.send(JSON.stringify(message));
  }

  isCurrentParticipant(participantId) {
    return participantId === get(this.store.getState(),
      'teevid.meeting.participant._id', null);
  }

  get canModerate() {
    const state = this.store.getState();
    const role = get(state, 'teevid.meeting.participant.role', null);
    return [roles.MODERATOR, roles.OWNER].indexOf(role) !== -1;
  }

  get isRoomOwner() {
    const state = this.store.getState();
    return get(state, 'teevid.meeting.participant.isRoomOwner', false);
  }

  hasOwnerPin() {
    const state = this.store.getState();
    return get(state, 'teevid.meeting.roomOptions.hasOwnerPin', false);
  }

  Stream(options, altHelper) {
    /** @private @type {Stream} */
    const stream = Erizo.UnboundStream(altHelper, options);
    // make sure attributes is an object
    const attributes = stream.getAttributes() || {};
    stream.updateLocalAttributes(attributes);
    rebrandStream(stream);
    stream.setAttribute = function (name, value) {
      const attributes = stream.getAttributes() || {};
      if (attributes[name] !== value) {
        attributes[name] = value;
        stream.setAttributes(attributes);
      }
      return attributes;
    };
    return stream;
  }
  get serverUrl() {
    const { serverUrl } = this.store.getState().teevid.user;
    return serverUrl;
  }
  verifyDeveloperKey() { }
  init(serverUrl, store) {
    this.store = store;
    this.actions = bindActionCreators(actions, store.dispatch);
    this.cleanActions = actions;
    this.actions.setServerUrl(serverUrl);
    this.actions.refreshMediaDevices();
    apiCallHelper(serverUrl, null, 'get', '/system/configurations/public')
      .then(config => {
        this.actions.setGlobalConfig(config.payload);
      });
  }
  login(username, password) {
    this.actions.login(username, password);
  }

  _call(method, url, data, withToken) { // call as user
    const { serverUrl, userToken } = this.store.getState().teevid.user;
    let token = userToken;
    if (!token) {
      const { participantToken } = this.store.getState().teevid.meeting;
      token = participantToken;
    }
    return apiCallHelper(serverUrl, token, method, url, data)
      .then((result) => {
        // prevent token from expiration if we got a new one from server
        if (withToken) {
          return result;
        } else {
          if (result.token) {
            const tokenData = parseJwt(result.token);
            if (!['Participant', 'Moderator'].includes(tokenData.role)) {
              this.actions.updateToken(result.token);
            }
          }
          return result.payload;
        }
      });
  }

  prepareUploadOptions(file) {
    const data = new FormData();
    data.append('file', file);

    const options = {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'cache-control': 'no-store'
      },
      body: data
    };

    const { userToken } = this.store.getState().teevid.user;

    if (userToken)
      options.headers['Authorization'] = 'Bearer ' + userToken;

    return options;
  }

  _upload(url, file) {
    const { serverUrl } = this.store.getState().teevid.user;

    if (file) {
      const options = this.prepareUploadOptions(file);

      return fetch(`https://${serverUrl}/api/v1${url}`, options);
    }

    return Promise.resolve(false);
  }

  get translator() {
    return {
      // TODO: use helper instead of callApiAsParticipant
      languages: () => callApiAsParticipant('get', '/translator/languages'),

      translate: (from, to, text) => {
        if (!to || !text) {
          throw Error('400 Bad Request');
        }

        return this._call(
          'post',
          `/translator/?from=${encodeURIComponent(from || '')}&to=${encodeURIComponent(to)}`, { message: text }
        );
      }

    };
  };

  /**
   * @prop {Object} configuration
   */

  get configuration() {
    this.filterDevices = (devices = [], type = 'videoinput') => devices.filter(d =>
      d.kind === type);
    this.configuration = { _items: false };

    return {

      get: () => this._call('get', '/systems/configurations/public')
        .then(conf => {
          this.configuration._items = {};
          conf.forEach(item => this.configuration._items[item._id] = item.value);
          return this.configuration._items;
        }),

      getMcuVersion: () => this._call('get', '/system/version'),

      listMediaDevices: () => {
        if (navigator.mediaDevices && navigator.mediaDevices.enumerateDevices) {
          return navigator.mediaDevices.enumerateDevices()
            .then(devices => {
              const videoIn = this.filterDevices(devices, 'videoinput');
              const audioIn = this.filterDevices(devices, 'audioinput');
              const audioOut = this.filterDevices(devices, 'audiooutput');
              return { audioIn, videoIn, audioOut };
            });
        }
        return Promise.resolve({ audioIn: [], videoIn: [], audioOut: [] });
      },

      listVideoInputs: () => {
        if (navigator.mediaDevices && navigator.mediaDevices.enumerateDevices) {
          return navigator.mediaDevices.enumerateDevices()
            .then(devices => this.filterDevices(devices, 'videoinput'));
        }
        return Promise.resolve([]);
      },

      listAudioInputs: () => {
        if (navigator.mediaDevices && navigator.mediaDevices.enumerateDevices) {
          return navigator.mediaDevices.enumerateDevices()
            .then(devices => this.filterDevices(devices, 'audioinput'));
        }
        return Promise.resolve([]);
      },

      listAudioOutputs: () => {
        if (navigator.mediaDevices && navigator.mediaDevices.enumerateDevices) {
          return navigator.mediaDevices.enumerateDevices()
            .then(devices => this.filterDevices(devices, 'audiooutput'));
        }
        return Promise.resolve([]);
      }
    };

  };

  get user() {

    return {
      guest: () => this._call('get', '/token/guest', null, true)
        .then(({ payload, token }) => {
          this.actions.loginSuccess(payload, token);
          return payload;
        }),

      editProfile: profile => this._call('patch', '/users/me', profile),

      refreshProfile: () => this._call('get', '/users/me')
        .then(profile => {
          if (profile)
            this.actions.setProfile(profile);
          return profile;
        }),

      setAvatar: file => this._upload('/users/avatar', file),

      /**
       * Request password recovery
       * Password recovery token will be sent to specified email
       * 
       * @param {string} email 
       * @returns {Promise}
       */

      requestPasswordRecovery: email => this._call('post',
        '/users/password-recovery', { email }),

      /**
       * Reset password using recovery token
       * 
       * @param {string} recoveryToken = token 
       * @param {string} password = newPassword 
       * @returns {Promise}
       */

      resetPassword: (recoveryToken, password) => this._call(
        'post',
        '/users/password-reset', {
          recoveryToken,
          password,
          type: 'password-recovery'
        }
      ),

      editPersonalRoom: roomData => {
        // TODO: get profile from state
        if (this.profile && this.profile.room) {
          return this._call('patch', '/rooms/my', roomData);
        }
        throw Error('You don\'t have a personal room');
      }
    };
  }

  get media() {
    return {

      requestStats: (kuid) => {
        if (!kuid) throw Error('You have to provide server ID');
        return this._call('get', `/media/stats/${kuid}`);
      },

      /**
       * Get a list of available media servers
       * @returns {Promise<Array>}
       */

      getServers: () => this._call('get', '/media/servers'),

      /**
       * Get info about a single KMS instance
       * @param {String} kuid - server ID
       */

      getServer: kuid => this._call('get', `/media/server/${kuid}`),

      /**
       * Get info about streaming availability and status
       * @param {String} room - server ID
       */

      checkStatus: room => this._call('get', `/media/check/${room}`),

      /**
       * Send a request to media server to start streaming
       * @param {String} room - room name
       * @returns {Promise}
       */

      startStreaming: room => {
        if (!room) throw Error('You must provide proper server and room IDs');
        return this._call('post', `/media/start/${room}`);
      },
      /**
       * Send a request to media server to stop streaming
       * @param {String} kuid - server ID
       * @param {String} streamId - id of a stream you want to stop
       * @returns {Promise}
       */

      stopStreaming: (room) => {
        if (!room) throw Error(
          'You must provide proper room ID');
        return this._call('post', '/media/stop', { room });
      },

      startRecording(room, path) {
        if (!room) throw Error('You must provide proper room ID');
        return this._call('post', '/record/start', { room, recordPath: path });
      },

      stopRecording(room) {
        if (!room) throw Error('You must provide proper room ID');
        return this._call('post', '/record/stop', { room });
      },

      /**
       *Call to SIP client
       * @param {String} room - current room
       * @param {String} to - SIP-client URI you want to call
       * @returns {Promise}
       */

      callSip: (room, to) => this._call('post', '/media/call', { room, to })
    };
  }

  get users() {
    return {
      list: () => this._call('get', '/users'),
      create: (data) => this._call('post', '/users', data),
      edit: (id, user) => {
        if (!id) throw Error('User does not exist');
        return this._call('patch', `/users/${encodeURIComponent(id)}`, user);
      },
      remove: (name) => this._call('delete', `/users/${encodeURIComponent(name)}`),
      changePassword: (data) => this._call('put',
        `/users/${encodeURIComponent(data.name)}/password`, data),
      deleteChosen: (nameData) => this._call('post', '/users/remove', nameData)
    };
  }

  get storyboards() {
    return {
      list: () => this._call('get', '/storyboard'),
      /**
       * Get single Storyboard
       *
       * @param {string} stbId
       * @returns {Promise<Storyboard>}
       */
      get: (stbId) => {
        if (!stbId) throw Error('Storyboard does not exist');
        return this._call('get', `/storyboard/${encodeURIComponent(stbId)}`);
      },

      /**
       * Get theme presets
       * 
       * @returns {Promise<[Theme]>}
       */
      listThemes: () => this._call('get', '/themes'),

      /**
       * Update storyboard data
       * 
       * @param {string} stbId - storyboard ID
       * @param {Storyboard} data - data to update
       * @returns {Promise<Storyboard>}
       */
      edit: (stbId, data) => {
        if (!stbId) throw Error('Storyboard does not exist');
        return this._call('post', `/storyboard/${encodeURIComponent(stbId)}`,
          data);
      },

      /**
       * Clone storyboard
       *
       * @param {string} stbId - storyboard ID
       * @param {Storyboard} data - data to update
       * @returns {Promise<Storyboard>}
       */
      clone: (stbId) => {
        if (!stbId) throw Error('Storyboard does not exist');
        return this._call('patch', `/storyboard/${encodeURIComponent(stbId)}/clone`);
      },

      /**
       * Remove Storyboard from DB
       * 
       * @param {string} stbId 
       * @returns {Promise}
       */
      remove: stbId => {
        if (!stbId) throw Error('Storyboard does not exist');
        return this._call('delete', `/storyboard/${encodeURIComponent(stbId)}`);
      },

      /**
       * Get layout presets from DB
       * 
       * @returns {Promise}
       */

      getLayouts: () => this._call('get', '/layouts'),

      /**
       * Create new storyboard from JSON data
       * 
       * @param {Storyboard} data 
       * @returns {Promise<Storyboard>}
       */

      create: data => this._call('post', '/storyboard', data),

      /**
       * Create storyboard from presentation file
       * 
       * @param {File} file 
       * @returns {Promise<Storyboard|boolean>}
       */

      upload: file => this._upload('/upload', file),

      /**
       * Create storyboard from presentation file with progress
       *
       * @param {File} file
       * @returns {Promise<Storyboard|boolean>}
       */

      uploadWithProgress: (file, onUploadProgress) => {
        const { serverUrl } = this.store.getState().teevid.user;
        if (file) {
          const options = this.prepareUploadOptions(file);
          return new Promise((resolve, reject) => {
            const xhr = new XMLHttpRequest();
            xhr.open('post', 'https://' + serverUrl + '/api/v1/upload');
            for (let k in options.headers || {})
              xhr.setRequestHeader(k, options.headers[k]);
            xhr.onload = () => {
              //emulated fetch response to have no difference in next flow
              resolve({
                body: xhr.responseText,
                status: xhr.status,
                json: function () {
                  return new Promise((resolve, reject) => {
                    try {
                      resolve(JSON.parse(this.body));
                    } catch (e) {
                      reject(e);
                    }
                  });
                }
              });
            };
            xhr.onerror = reject;
            if (xhr.upload && onUploadProgress) {
              xhr.upload.onprogress = onUploadProgress;
            }
            xhr.send(options.body);
          });
        }

        return Promise.resolve(false);
      },

      /**
       * Upload an image to the server
       * @param {File} file 
       * @returns {Promise<{url: String}>}
       */

      uploadImage: file => this._upload('/upload/image', file),

      /**
       * Get scenes from storyboard
       * 
       * @param {string} stbId - Storyboard ID
       * @returns {Promise<Array<Scene>>}
       */

      scenesList: stbId => {
        if (!stbId) throw Error('Storyboard does not exist');
        return this._call('get',
          `/storyboard/${encodeURIComponent(stbId)}/scenes`);
      },

      /**
       * Edit scene
       * 
       * @param {string} stbId 
       * @param {string} sceneId 
       * @param {Scene} data 
       * @returns {Promise<Scene>}
       */

      sceneEdit: (stbId, sceneId, data) => {
        if (!stbId) throw Error('Storyboard does not exist');
        if (!sceneId) throw Error('Scene does not exist');
        return this._call('patch',
          `/storyboard/${encodeURIComponent(stbId)}/scenes/${encodeURIComponent(sceneId)}`,
          data
        );
      },

      /**
       * Remove scene from storyboard
       * 
       * @param {string} stbId 
       * @param {string} sceneId 
       * @returns {Promise}
       */

      sceneRemove: (stbId, sceneId) => {
        if (!stbId) throw Error('Storyboard does not exist');
        if (!sceneId) throw Error('Scene does not exist');
        return this._call('delete',
          `/storyboard/${encodeURIComponent(stbId)}/scenes/${encodeURIComponent(sceneId)}`
        );
      },

      /**
       * Add scene to storyboard
       * 
       * @param {string} stbId 
       * @param {Scene} data 
       * @returns 
       */

      sceneCreate: (stbId, data) => {
        if (!stbId) throw Error('Storyboard does not exist');
        return this._call('post', `/storyboard/${encodeURIComponent(stbId)}/scenes`,
          data);
      }

    };

  }

  get rooms() {
    return {

      /**
       * Gets the room details from the TeeVid database.
       *
       * Corresponds to the /rooms/:roomId [GET] API endpoint.
       * @param {string} roomId
       * @returns {Promise<Room>}
       */

      get: roomId => {
        if (!roomId) throw Error('Room does not exist');
        return this._call('get', `/rooms/${encodeURIComponent(roomId)}`);
      },

      /**
       * Gets the room token you can use to login to the room using `TeeVid.participants.createFromToken`.
       *
       * Corresponds to the /rooms/:roomId/tokens [GET] API endpoint.
       * @param {string} roomId
       * @returns {Promise<string>}
       */

      getInviteToken: roomId => {
        if (!roomId) throw Error('Room does not exist');
        return this._call('get', `/rooms/${encodeURIComponent(roomId)}/tokens`)
          .then(res => res.token);
      },

      /**
       * Gets the details for all the rooms in the TeeVid database.
       *
       * Corresponds to the /rooms [GET] API endpoint.
       * @returns {Promise<Room[]>}
       */

      list: () => this._call('get', '/rooms'),

      /**
       * Create a new room in the TeeVid database.
       *
       * Corresponds to the /rooms [POST] API endpoint.
       * @param {Room} room
       * @returns {Promise<Room>}
       */

      create: room => this._call('post', '/rooms', room),

      /**
       * Edit a room in the TeeVid database.
       *
       * Corresponds to the /rooms/:roomId [PATCH] API endpoint.
       * @param {id} string
       * @param {Room} room
       * @returns {Promise<Room>}
       */

      edit: (id, room) => {
        if (!id) throw Error('Room does not exist');
        return this._call('patch', `/rooms/${encodeURIComponent(id)}`, room);
      },

      /**
       * Delete a room in the TeeVid database.
       *
       * Corresponds to the /rooms/:roomId [DELETE] API endpoint.
       * @param {string} roomId
       * @returns {Promise<Room>} - the room just deleted
       */
      delete: roomId => {
        if (!roomId) throw Error('Room does not exist');
        return this._call('delete', `/rooms/${encodeURIComponent(roomId)}`);
      },
      deleteChosen: (nameArray) => this._call('post', '/rooms/remove', nameArray)

    };
  }

  get participants() {
    return {
      list: roomId => {
        if (!roomId) throw Error('Room does not exist');
        return this._call('get',
          `/rooms/${encodeURIComponent(roomId)}/participants`);
      },

      /**
       * Delete (kick) a participant from a room.
       *
       * Corresponds to the /rooms/:roomId/participants/:participantId [DELETE] API endpoint, you must be a room Moderator
       * (participant with Moderator role) or an administrator.
       * @param {string} roomId
       * @param {string} participantId
       * @returns {Promise<Object>} TODO
       */

      delete: (roomId, participantId) => {
        if (!roomId) throw Error('Room does not exist');
        if (!participantId) throw Error('Participant does not exist');
        if (participantId.indexOf('local_') === 0) throw Error(
          'Paricipant does not exist');
        return this._call('delete',
          `/rooms/${encodeURIComponent(roomId)}/participants/${encodeURIComponent(participantId)}`
        );
      },

      /**
       * Delete (kick) all participants from a room.
       *
       * Corresponds to the /rooms/:roomId/participants [DELETE] API endpoint, you must be a room Moderator 
       * (participant with Moderator role) or an administrator.
       * @param {string} roomId
       * @returns {Promise<Object>}
       */

      deleteAll: roomId => {
        if (!roomId) throw Error('Room does not exist');
        return this._call('delete',
          `/rooms/${encodeURIComponent(roomId)}/participants`);
      },

      /**
       * Try to become room owner. Note that the accessToken will be checked and this could be called only 
       * if you have an active connection (done with `create`),
       * then you can use this function to become actually owner.
       *
       * Corresponds to the /rooms/:roomId/participants/:participantId [PATCH] API endpoint.
       * @param {string} roomId
       * @param {string} participantId
       * @param {string} pin
       * @returns {Promise<string>} new role if everything went right (Moderator)
       */

      updateRole: (roomId, participantId, pin) => {
        if (!roomId) throw Error('Room does not exist');
        if (!participantId) throw Error('Participant duplicated');
        if (!pin) throw Error('Bad pin');
        return this._call('patch',
          `/rooms/${encodeURIComponent(roomId)}/participants/${encodeURIComponent(participantId)}`, { pin }
        ).then(participant => {
          if (participant.role === 'Paricipant') throw Error('Bad pin');
          return participant.role;
        });
      },

      /**
       * Change participant name, note that the accessToken will be checked and this could be called only if you have an active connection.
       *
       * Corresponds to the /rooms/:roomId/participants/:participantId [PATCH] API endpoint.
       * @param {string} roomId
       * @param {string} participantId
       * @param {string} name
       * @returns {Promise<string>} new name if everything went right
       */

      updateName: (roomId, participantId, name) => {
        if (!roomId) throw Error('Room does not exist');
        if (!participantId) throw Error('Participant duplicated');
        return this._call('patch',
          `/rooms/${encodeURIComponent(roomId)}/participants/${encodeURIComponent(participantId)}`, { name }
        );
      }

    };
  }
  get license() {
    return {
      get: () => this._call('get', '/system/license'),
      update: (license) => this._call('put', '/system/license', { license })
    };
  }
  get system() {
    return {
      servers: () => this._call('get', '/system/servers'),
      liveMeetings: () => this._call('get', '/live-meetings'),
      /**
       * Get the system status.
       *
       * Corresponds to the /system/status [GET] API endpoint.
       * @returns {Promise<Object>}
       */
      status: () => this._call('get', '/system/status'),
      setLogo: (file) => this._upload('/system/logo', file)
    };
  }
  get reports() {
    return {
      meetings: (data) => this._call('get', `/meetings?from=${data.from}&to=${data.to}`),
      participants: (data) => this._call('get', `/meetings-participants?from=${data.from}&to=${data.to}`)
    };
  }
  get mcu() {
    return {
      list: () => this._call('get', '/system/mcu/list'),
      start: (id) => this._call('put', `/system/mcu/start/${encodeURIComponent(id)}`),
      stop: (id) => this._call('put', `/system/mcu/stop/${encodeURIComponent(id)}`),
      kill: (id) => this._call('put', `/system/mcu/kill/${encodeURIComponent(id)}`),
      restart: (id) => this._call('put', `/system/mcu/restart/${encodeURIComponent(id)}`),
      remove: (id) => this._call('delete', `/system/mcu/${encodeURIComponent(id)}`)
    };
  }
  get mediaServers() {
    return {
      list: () => this._call('get', '/system/servers'),
      add: (data) => this._call('post', '/system/servers', data),
      delete: (data) => {
        if (Array.isArray(data)) {
          return this._call('delete', `/system/servers/${data.join('&')}`);
        } else {
          return this._call('delete', `/system/servers/${data}`);
        }
      }
    };
  }
  get kurentoServers() {
    return {
      list: () => this._call('get', '/media/servers')
    };
  }
}

const Instance = new TeeVid();

window.TeeVidSdk = Instance;

export default Instance;
