import {
  all,
  call,
  put,
  take,
  takeEvery,
  select,
  fork,
  cancel
} from 'redux-saga/effects';
import { eventChannel, delay } from 'redux-saga';
import actions from './actions';
import types, { roles } from './types';
import {
  callApiAsUser,
  rebrandStream,
  mSocketSend
} from '../utils';
import apiActions from '../api/actions';
import Erizo from '../libs/erizo';
import { statsMonitor } from './stats';
import {
  serverUrlSelector,
  participantSelector,
  connectionSelector,
  roomSelector,
  configsSelector,
  hasAccessPinSelector,
  meetingModeSelector
} from './selectors';
import { RoomEvents } from './eventTypes';
import moderationActions from '../moderationActions';
import userActions from '../user/actions';
import { ConnectionStates, modes } from '../constants';
import { moderationMessageHandler } from './moderationSaga';

const setLocalStorage = (key, value) => localStorage.setItem(key, value);

// ! needed to close connection and moderation handler tasks. Not pretty but it works
let cTask = null;
let mTask = null;

export function createConstraints() {
  let options = { audio: false, video: false };
  // return navigator.mediaDevices && navigator.mediaDevices.enumerateDevices &&
  // TODO: check if it's even supported
  return navigator.mediaDevices.enumerateDevices()
    .then(media => {
      const audioInputs = media.filter(d => d.kind === 'audioinput');
      const videoInputs = media.filter(d => d.kind === 'videoinput');

      const audio = Boolean(audioInputs.length);
      let video = Boolean(audio && videoInputs.length);

      if (audio && video) {
        options = { audio: true, video: true };
      } else if (audio && !video) {
        options = { audio: true, video: false };
      } else if (video && audioInputs.length) {
        options = { audio: true, video: true };
      } else {
        options = { audio: false, video: false };
      }
      setLocalStorage('options', JSON.stringify(options));
      return options;
    })
    .catch(error => console.log(error));
};
export function* connectSaga() {
  let participant;
  while (true) {
    const action = yield take([
      types.CREATE_PARTICIPANT,
      types.CONNECTION_CANCEL,
      types.CONNECTION_DISCONNECTED
    ]);
    const { connectionState } = yield select(state => state.teevid.meeting);
    if ((action.type === types.CONNECTION_CANCEL)
      || (action.type === types.CONNECTION_DISCONNECTED)) {
      if (participant) {
        yield participant.cancel();
        participant = null;
        if (cTask) { yield cTask.cancel(); cTask = null; }
        if (mTask) { yield mTask.cancel(); mTask = null; }
      }
      yield put(actions.setConnectionState(ConnectionStates.DISCONNECTED));
    } else {
      if (connectionState === ConnectionStates.IN_WAITING_ROOM) {
        if (participant)
          yield cancel(participant);
        yield put(actions.setConnectionState(ConnectionStates.CONNECTING));
        participant = yield fork(createParticipant, action.payload);
      }
      if (!participant && (connectionState !== ConnectionStates.CONNECTING)) {
        yield put(actions.setConnectionState(ConnectionStates.CONNECTING));
        participant = yield fork(createParticipant, action.payload);
      }
    }
  }
}

function* createParticipant({ username, pin, room: roomId }) {
  // console.log('# creating participant');
  const requiresAccessPin = yield select(hasAccessPinSelector);
  try {
    const participant = yield call(waitForParticipant, { username, pin, roomId });
    yield put(actions.setCurrentRoom(participant.room));
    yield put(actions.participantCreated(participant));
  } catch (error) {
    console.log('Error creating participant: ', error.message);
    const toDo = [put(actions.connectionCancel())];
    if (error.message === 'err-bad-pin') {
      if (requiresAccessPin) {
        // toDo.push(put(notifActions.notifSend({
        //   message: locale('err-bad-pin'),
        //   kind: 'warning',
        //   dismissAfter: 2000
        // })));
      }
    } else if (error.message === 'token-expired-error') {
      // TODO: logout
      toDo.push(put(userActions.logout()));
    } else {
      // TODO: dispatch general error message
      toDo.push(put(actions.connectionError(error)));
    }

    yield all(toDo);
  }
}

function* waitForParticipant({ username, pin, roomId }) {
  while (true) {
    try {
      const { payload: participant } = yield call(callApiAsUser, 'post',
        `/rooms/${roomId}/participants/`, {
          username,
          pin
        });
      return participant;
    } catch (error) {
      switch (error.code) {
        case 412:
          const toDo = [
            put(actions.setAccessPinRequirement(false))
          ];
          if (error.message === 'no-slots-error') {
            toDo.unshift(put(actions.setConnectionState(ConnectionStates
              .IN_WAITING_SLOT)));
          } else {
            toDo.unshift(put(actions.setConnectionState(ConnectionStates
              .IN_WAITING_ROOM)));
          }
          yield all(toDo);
          yield call(delay, 2000);
          break;
        case 401:
          if (['license-error-invalid', 'license-error-missing'].indexOf(error.message) === -1) {
            yield put(actions.setAccessPinRequirement(true));
          }
          throw error;
        default:
          throw error;
      }
    }
  }
}

function createModerationChannel(moderationSocket, connection) {
  return eventChannel((emit) => {
    const messageHandler = (socketEvent) => {
      var message = socketEvent.data;
      if (message === '_') { // this client is alive
        moderationSocket.send(message);
      } else {
        message = JSON.parse(message);
        emit(message);
      }
    };
    moderationSocket.onerror = (error) => {
      // emit(error);
      if (connection.state === 2) {
        return moderationSocket.onclose(null);
      }
    };
    moderationSocket.onmessage = messageHandler;
    return () => { moderationSocket.close(); };
  });
}

function createConnectionChannel(connection) {
  const eventTypes = [
    'room-connected',
    'stream-added',
    'stream-subscribed',
    'stream-removed',
    'room-error',
    'room-disconnected'
  ];

  return eventChannel((emit) => {
    function connectionEventHandler(type, event) {
      emit({
        type,
        event
      });
    }
    eventTypes.forEach(type => {
      connection.addEventListener(type,
        connectionEventHandler.bind(null, type));
    });
    return () => { connection.disconnect(); };
  });
}

function* connectionEventHandler(channel) {
  yield takeEvery(types.CONNECTION_DISCONNECT, () => {
    channel.close();
    // console.log('@ close connection channel');
  });
  yield takeEvery(channel, function* (message) {
    // console.log('@ event', message);
    let action = actions.noop();
    switch (message.type) {
      case RoomEvents.ROOM_CONNECTED:
        action = actions.roomConnected();
        break;
      case RoomEvents.STREAM_ADDED:
        action = actions.streamAdded(message.event.stream);
        break;
      case RoomEvents.STREAM_SUBSCRIBED:
        action = actions.streamSubscribed(message.event.stream);
        break;
      case RoomEvents.STREAM_REMOVED:
        action = actions.streamRemoved(message.event.stream);
        break;
      case RoomEvents.ROOM_DISCONNECTED:
        action = actions.disconnected();
        break;
      default:
        break;
    }
    yield put(action);
  });
}

function* prepareForConnection() {
  yield takeEvery(types.CONNECT, function* (action) {
    const serverUrl = yield select(serverUrlSelector);
    const participant = yield select(participantSelector);
    const connection = yield select(connectionSelector);
    const options = yield createConstraints();
    yield put(apiActions.refreshCurrentMediaDevices(options));
    yield put(actions.statusChanged({
      hasMic: options.audio,
      hasCam: options
        .video
    }));

    const socketUrl =
      `wss://${serverUrl}/socket/moderation?token=${encodeURIComponent(participant.token)}`;

    const moderationSocket = new WebSocket(socketUrl);
    moderationSocket.onclose = (reason) => {
      console.info('Moderation Socket closed', reason);
      TeeVidSdk.actions.disconnect();
    };
    const moderationChannel = createModerationChannel(moderationSocket,
      connection);
    const connectionChannel = createConnectionChannel(connection);
    yield put(actions.moderationSocketCreated(moderationSocket));
    mTask = yield fork(moderationMessageHandler, moderationChannel);
    cTask = yield fork(connectionEventHandler, connectionChannel);
  });

  yield takeEvery(types.MODERATION_SERVER_READY, function* () {
    const participantState = yield select(participantSelector);
    const meetState = yield select(meetingModeSelector);
    if (participantState.role === roles.MODERATOR && meetState.storyboardId && !meetState.lecturerId && meetState.mode === modes.LECTURE) {
      yield mSocketSend(moderationActions.participantReady, participantState, meetState);
    } else {
      yield mSocketSend(moderationActions.participantReady, participantState);
    }

  });
}

function* createConnection() {
  yield takeEvery(types.CREATE_CONNECTION, function* () {
    const participant = yield select(participantSelector);
    const room = yield select(roomSelector);
    const configuration = yield select(configsSelector);
    const roomSpec = { token: participant.nuveId };
    const connection = Erizo.Room(roomSpec);
    const _publishFn = connection.publish;
    // const { bandwidth, resolution } = room;
    connection.publish = function (stream, options, cb) {
      options = options || {};
      options.scheme = 'notify-break-recover';

      if (stream.hasVideo()) {
        let layers = 0;
        if (room.simulcastLayers === 'auto') {
          layers = parseInt(configuration.simulcast_layers, 10);
        } else {
          layers = parseInt(room.simulcastLayers, 10);
        }
        stream.simulcastLayers = layers;

        if (layers > 1 && window.chrome) {
          // const bw = bandwidth === 'auto' ? defaultBW*1024 : parseInt(room.bandwidth)*1024;
          // const bitrates = (layers === 2) ? { 0: 0.2*bw, 1: 0.8*bw } : { 0: 0.1*bw, 1: 0.3*bw, 2: 0.6*bw };
          // const bitrates = (layers === 2) ? { 0: 30000, 1: 470000 } : { 0: 30000, 1: 120000, 2: 450000 };
          options.simulcast = {
            numSpatialLayers: layers
            // spatialLayerBitrates: bitrates
          };
          stream.simulcastLayers = layers;
        }
      }
      var streamAttrs = stream.getAttributes() || {};
      streamAttrs.participantId = participant._id;
      streamAttrs.username = participant.name;
      stream.updateLocalAttributes(streamAttrs);
      // TODO: return promise
      return _publishFn.apply(connection, [stream, options, cb]);
    };

    yield put(actions.connectionCreated(connection));

  });
}

function* meetingStateHandler() {
  yield takeEvery(types.SET_MEETING_STATE, function* () {
    yield put(actions.connectionReady());
  });
}

function* roomConnectionHandler() {
  yield takeEvery(types.ROOM_CONNECTED, function* () {
    const connection = yield select(connectionSelector);
    connection.remoteStreams.forEach((stream) => {
      if (!stream.local) {
        rebrandStream(stream);
        connection.subscribe(stream);
      }
    });
    // xxx: setup stats collection ==> only on local and screen?

    yield put(actions.connectionRoomReady());
    const monitor = yield fork(timer);
    yield take('stop-timer');
    yield cancel(monitor);
  });
};

function* timer() {
  while (true) {
    const participant = yield select(participantSelector);
    if (!participant) {
      yield put({ type: 'stop-timer' });
    }
    yield statsMonitor();
    yield delay(3000);
  }
}

function* streamAddedHandler() {
  yield takeEvery(types.CONNECTION_STREAM_ADDED, function* ({ payload: stream }) {
    const connection = yield select(connectionSelector);
    if (!stream.local) {
      rebrandStream(stream);
      connection.subscribe(stream);
    }
  });
}

function* streamRemovedHandler() {
  yield takeEvery(types.CONNECTION_STREAM_REMOVED, function* ({ payload: stream }) {
    // if local stream = notify server
    const participant = yield select(participantSelector);
    const pStreamId = participant.streams.mediaStreamId;
    if (pStreamId === stream.getID()) {
      yield mSocketSend(moderationActions.streamRemoved, stream.streamID());
    }
  });
}

export default function* saga() {
  yield all([
    prepareForConnection(),
    connectSaga(),
    createConnection(),
    meetingStateHandler(),
    roomConnectionHandler(),
    streamAddedHandler(),
    streamRemovedHandler()
  ]);
}
