// Convert a bitrate into a 'XXX Xbps' string.
export function formatBitrate(value) {
  if (!value)
    return '- bps';

  let suffix;
  if (value < 1000)
    suffix = 'bps';
  else if (value < 1000000) {
    suffix = 'kbps';
    value /= 1000;
  } else {
    suffix = 'Mbps';
    value /= 1000000;
  }

  let str = value.toPrecision(3) + ' ' + suffix;
  return str;
}

export function formatDelay(value) {
  if (!value && value !== 0)
    return '';
  return parseFloat(value).toFixed(0) + ' ms';
}

export function computeQoe(rtt) {
  if (rtt / 2 >= 500)
    return 1;
  else if (rtt / 2 >= 400)
    return 2;
  else if (rtt / 2 >= 300)
    return 3;
  else if (rtt / 2 >= 200)
    return 4;
  return 5;
}

// Takes two stats reports and determines the rate based on two counter readings
// and the time between them (which is in units of milliseconds).
export function computeRate(newReport, oldReport, statName) {
  let newVal = newReport.stat(statName);
  let oldVal = (oldReport) ? oldReport.stat(statName) : null;
  if (!newVal || !oldVal)
    return null;
  return (newVal - oldVal) / (newReport.timestamp - oldReport.timestamp)
    * 1000;
}

// Convert a byte rate to a bit rate.
export function computeBitrate(newReport, oldReport, statName) {
  return computeRate(newReport, oldReport, statName) * 8;
}
