import { StreamingStates } from '../constants';
import { takeEvery, put, all, select, call } from 'redux-saga/effects';
import types from './types';
import actions from './actions';
import { callApiAsParticipant } from '../utils';
import {
  serverUrlSelector,
  participantSelector,
  connectionSelector,
  roomSelector,
  roomOptionsSelector,
  configsSelector,
  remoteParticipantsSelector,
  tokenSelector,
  socketSelector,
  streamsSelector
} from './selectors';

export function* checkStreamingSaga() {
  yield takeEvery(types.MEDIA_STREAM_CHECK, function*({ payload: room }) {
    try {
      const { isStreaming } = yield call(TeeVidSdk.media.checkStatus,
        room);
      if (isStreaming) {
        yield put(actions.startMediaStreamSuccess(isStreaming));
      } else {
        yield put(actions.setStreamStatus(StreamingStates.STOPPED));
      }
    } catch (error) {
      // xxx yield put(responseErrorHandler(error.message, error.status));
    }
  });
}

export function* startMediaStreamSaga() {
  yield takeEvery(types.STREAMING_START,
    function*() {
      try {
        const roomOptions = yield select(roomOptionsSelector);
        const room = yield select(roomSelector);
        if (roomOptions.enabledStreaming)
          yield all([
            call(
              callApiAsParticipant,
              'POST',
              '/media/start', { room: room.id }),
            put(actions.setStreamingState(StreamingStates.INIT))
          ]);
      } catch (error) {
        // xxx yield put(responseErrorHandler(error.message, error.status));
      }
    }
  );
}

export function* stopMediaStreamSaga() {
  yield takeEvery(types.STREAMING_STOP,
    function*() {
      try {
        const room = yield select(roomSelector);

        yield all([
          call(callApiAsParticipant,
            'POST',
            '/media/stop', {
              room: room.id
            }),
          put(actions.setStreamingState(StreamingStates.STOPPING))
        ]);
      } catch (error) {
        console.error(error);
        // xxx yield put(responseErrorHandler(error.message, error.status));
      }
    }
  );
}

export function* streamingSaga() {
  yield all([
    stopMediaStreamSaga(),
    startMediaStreamSaga()
    // checkStreamingSaga()
  ]);
}
