import { takeEvery, put, all, select } from 'redux-saga/effects';
import types from './types';
import actions from './actions';
import { callApiAsUser } from '../utils';
import { userCallOptionsSelector } from '../meeting/selectors';

function* loginSaga() {
  yield takeEvery(types.LOGIN, function* login({ payload: { username, password } }) {
    if (!username || !password)
      yield put(actions.loginError({ status: 403, message: 'bad-credentials' }));
    try {
      const { payload: profile, token } = yield callApiAsUser('post',
        '/accesstokens', {
          username,
          password
        });
      yield put(actions.loginSuccess(profile, token));
    } catch (error) {
      yield put(actions.loginError(error));
    }
  });
}

function* userProfileSaga(action) {
  yield takeEvery(types.USER_REFRESH_PROFILE, function* () {
    try {
      const { token } = yield select(userCallOptionsSelector);
      if (token) {
        const { payload } = yield callApiAsUser('get', '/users/me');
        yield put(actions.setProfile(payload));
      }
    } catch (error) {
      yield put(actions.loginError(error));
    }
  });
  yield takeEvery(types.USER_EDIT_PROFILE, function* ({ profile }) {
    try {
      const { token } = yield select(userCallOptionsSelector);
      if (token) {
        yield callApiAsUser('patch', '/users/me', profile);
        yield put(actions.refreshProfile());
      }
    } catch (error) {
      yield put(actions.editProfileError(error));
    }
  });
}

export default function* () {
  yield all([
    loginSaga(),
    userProfileSaga()
  ]);
};
