import types from './types';

const streamingStates = {
  INIT: 0,
  CONNECTED: 1,
  IS_STREAMING: 2,
  STOPPING: 3,
  STOPPED: 4,
  ERROR: 5
};

const initialState = {
  guestToken: '',
  room: '',
  serverUrl: '',
  name: '',
  moderationSocket: null,
  sent: null,
  reason: null,
  accepted: null,
  participant: null,
  streamingState: streamingStates.IS_STREAMING
};

export default (state =  initialState, { type, payload }) => {
  switch(type) {
    case types.OPEN_VIEWER_CHANNEL: 
      return Object.assign({}, state, { room: payload.roomId } );

    case types.VIEWER_CHANNEL_OPENED: 
      return Object.assign({}, state, { moderationSocket: payload });

    case types.REQUEST_TO_PARTICIPATE_SENT:
    case types.REQUEST_TO_PARTICIPATE_ACCEPTED:
    case types.REQUEST_TO_PARTICIPATE_REJECTED: 
      return Object.assign({}, state, payload);

    case types.CLEAR_REQUEST_DATA: 
      return Object.assign({}, state, { sent: null, reason: '', accepted: null });

    // Receiving initial meeting state:
    case types.SET_MEETING_STATE: 
      return Object.assign({}, state, { streamingState: payload.state.streamingState });

    // Receiving changed streaming status:
    case types.SET_RECEIVED_STREAMING_STATE: 
      return Object.assign({}, state, { streamingState: payload.newState });

    default:
      return state;
  }
};