import { formatBitrate, formatDelay, computeQoe } from './utils';
/** @private @param {Stream} stream */
export default class streamStatsFirefox {
  constructor(stream) {
    this.stream = stream;
    this.getRawStats = this.getRawStats.bind(this);

  }

  getRawStats() {
    return new Promise((resolve, reject) => {
      if (!this.stream.pc)
        return reject('invalid stream');
      return this.stream.pc.peerConnection.getStats()
        .then(rawStats => {
          resolve(rawStats);
        });
    });
  }

  compute () {
    let p;
    if (this._rawStats) {
      this._prevRawStats = this._rawStats;
      p = Promise.resolve(true);
    } else {
      p = this.getRawStats()
        .then((s) => {
          new Promise((resolve) => {
            setTimeout(() => {
              this._prevRawStats = s;
              resolve(true);
            }, 500);
          });
        });
    }
    return p
      .then(this.getRawStats)
      .then(rawStats => {
        this._rawStats = rawStats;
        let stats = []; // a collection of {label, direction, value, formatted}, where direction is rx or tx, and formatted is formatted value
        if (this.stream.simulcastLayers && this.stream.simulcastLayers > 1)
          stats.push({
            label: 'Simulcast Layers',
            name: 'stats-simulcast-layers',
            direction: 'tx',
            value: this.stream.simulcastLayers
          });
        let rtt = 0;

        rawStats.forEach(key => {
          let direction, type;
          if (key.id.indexOf('outbound_') === 0)
            direction = 'tx';
          else if (key.id.indexOf('inbound_') === 0)
            direction = 'rx';
          else
            return;
          let obj = key;
          switch (obj.mediaType) {
            case 'video':
              type = 'Video';
              break;
            case 'audio':
              type = 'Audio';
              break;
            default:
              break;
          }
          if (obj.roundTripTime && obj.roundTripTime > rtt)
            //convert sec to ms
            rtt = obj.roundTripTime * 1000;
          if (obj.bitrateMean && obj.bitrateMean > 0)
            stats.push({
              label: type + ' BW',
              name: 'stats-' + obj.mediaType
                + '-bw',
              direction: direction,
              value: obj.bitrateMean,
              formatted: formatBitrate(obj.bitrateMean)
            });

          //For audio bandwidth
          if (obj.bytesSent && obj.bytesSent > 0 && obj.mediaType === 'audio') {
            let rxPrevAudio = getStatsReport(this._prevRawStats, obj.type, obj.mediaType, 'bytesSent');
            let rxAudioBitrate = computeRate(obj, rxPrevAudio);
            
            stats.push({
              label: 'Audio BW',
              name: 'stats-audio-bw',
              direction: direction,
              value: obj.bytesSent,
              formatted: formatBitrate(rxAudioBitrate)
            });
          }
        });

        if (rtt > 0) {
          stats.unshift({
            label: 'RTT',
            name: 'stats-rtt',
            value: rtt,
            formatted: formatDelay(rtt)
          });
          stats.unshift({
            label: 'QOE',
            name: 'stats-qoe',
            value: computeQoe(
              rtt)
          });
        }
        return stats;
      });
  };
}

function getStatsReport(stats, statObj, mediaType, statName, statVal) {
  let result;
  if (stats) {
    stats.forEach(report => {
      if (report.type === statObj && report.mediaType === mediaType) {
        let found = true;
        // If |statName| is present, ensure |report| has that stat.
        // If |statVal| is present, ensure the value matches.
        if (statName) {
          let val = report[statName];
          found = (statVal !== undefined) ? (val === statVal) : val;
          if (found)
            result = report;
        }
      }
    });
  }
  return result;
}

function computeRate(newReport, oldReport) {
  let newVal = newReport.bytesSent;
  let oldVal = oldReport ? oldReport.bytesSent : null;
  if (!newVal || !oldVal)
    return null;
  return ((newReport.bytesSent - oldReport.bytesSent) / (newReport.timestamp - oldReport.timestamp) * 1000) * 8;
}