import { all, put, takeEvery, select, takeLatest } from 'redux-saga/effects';
import * as selectors from './selectors';
import actions from './actions';
import apiActions from '../api/actions';
import moderationActions, { methods } from '../moderationActions';
import { mSocketSend } from '../utils';
import { delay } from 'redux-saga';
import types, { deviceTypes, SDKMessages } from './types';
import userTypes from '../user/types';
import { generateThumbnail } from '../utils';
import { lectureLayouts, modes, layoutModes, CallStates } from '../constants';
import {
  participantSelector,
  remoteParticipantsSelector,
  modeSelector,
  muteStateSelector,
  canModerateSelector,
  hasOwnerPinSelector,
  meetingLayoutSelector,
  meetingModeSelector,
  layoutGeometryOptionsSelector,
  storyboardStateSelector
} from './selectors';

const getRoomGeometry = (room) => {
  let geometry;
  if (room) {
    const { optimized_layout_full_quality, optimized_layout_threshold } = room;
    geometry = Object.assign({}, lectureLayouts['default'].geometry, {
      optimized_layout_full_quality: isNaN(optimized_layout_full_quality) ? 1 : parseInt(optimized_layout_full_quality),
      optimized_layout_threshold: isNaN(optimized_layout_threshold) ? 1 : parseInt(optimized_layout_threshold)
    });
  } else {
    geometry = Object.assign({}, lectureLayouts['default'].geometry);
  }
  return {
    optimizedLayoutFullQuality: geometry.optimized_layout_full_quality,
    optimizedLayoutThreshold: geometry.optimized_layout_threshold
  };
};

function* getLayoutGeometry() {
  const { room, globalConfigs, lectureLayout, mode, layoutMode } =
    yield select(layoutGeometryOptionsSelector);
  const optimizedLayoutFullQuality = isNaN(globalConfigs.optimized_layout_full_quality) ? 1 : parseInt(globalConfigs.optimized_layout_full_quality);
  const optimizedLayoutThreshold = isNaN(globalConfigs.optimized_layout_threshold) ? 1 : parseInt(globalConfigs.optimized_layout_threshold);
  const defaultValues = Object.assign({
    optimizedLayoutFullQuality,
    optimizedLayoutThreshold
  }, getRoomGeometry(room));
  if ((mode === modes.BASIC_LECTURE || lectureLayout) && layoutMode === layoutModes.MANUAL) {
    return {
      optimizedLayoutFullQuality: lectureLayouts[lectureLayout]
        ? lectureLayouts[lectureLayout].geometry
          .optimized_layout_full_quality
        : defaultValues.optimizedLayoutFullQuality,
      optimizedLayoutThreshold: lectureLayouts[lectureLayout]
        ? lectureLayouts[lectureLayout].geometry.optimized_layout_threshold
        : defaultValues.optimizedLayoutThreshold
    };
  } else {
    return defaultValues;
  }
}

function* updateLayout() {
  const geometry = yield getLayoutGeometry();
  yield put(actions.updateLayoutGeometry(geometry));
  yield put(actions.updateLayoutParticipants());
  yield delay(500);
  yield put(actions.sendMeetingLayout());
}

function* sendLayout() {
  const mode = yield select(modeSelector);
  if (mode === modes.BASIC_LECTURE || mode === modes.LECTURE) {
    const layout = yield select(meetingLayoutSelector);
    yield mSocketSend(moderationActions.setLayout, layout);
  }
  yield;
}

function* meetingSetMode() {
  const participant = yield select(participantSelector);
  if (participant) {
    const geometry = yield getLayoutGeometry();
    yield put(actions.updateLayoutGeometry(geometry));
    yield put(actions.updateLayoutParticipants());
    const mode = yield select(meetingModeSelector);

    yield mSocketSend(moderationActions.setMode, mode);
  }
}

function* selectForLecture({ payload }) {
  const meeting = yield select(meetingModeSelector);
  const participant = yield select(participantSelector);
  let mutedParticipant = null,
    unmutedParticipant = null;
  if (meeting.lecturerId
    && meeting.lecturerId !== participant._id)
    return yield;

  const targetValue = meeting.selectedForLecture[payload.tileId];
  const sourceIndex = meeting.selectedForLecture.indexOf(payload.participantId);
  if (targetValue === participant._id && (sourceIndex === -1
    || payload.participantId === 'stub'))
    return yield; // don't remove local stream

  if ((targetValue !== 'stub' && (sourceIndex === -1 || payload.participantId
    === 'stub'))) {
    mutedParticipant = targetValue;
  }
  if (sourceIndex === -1) {
    unmutedParticipant = payload.participantId;
  }
  if (sourceIndex !== -1 && payload.participantId !== 'stub') {
    meeting.selectedForLecture[sourceIndex] = targetValue;
  }
  meeting.selectedForLecture[payload.tileId] = payload.participantId;
  yield put(actions.updateLayoutParticipants(meeting.selectedForLecture));
  if (meeting.mode === modes.BASIC_LECTURE) {
    if (mutedParticipant) {
      yield mSocketSend(moderationActions.mute, participant._id,
        mutedParticipant, {
          audio: true,
          video: true
        });
    }
    if (unmutedParticipant) {
      yield mSocketSend(moderationActions.mute, participant._id,
        unmutedParticipant, {
          audio: false,
          video: false
        });
    }
  }
  yield put(actions.sendMeetingLayout());
}

function* selectForPanelMode({ payload: { selectedForLecture } }) {
  const meeting = yield select(meetingModeSelector);
  const participant = yield select(participantSelector);
  yield put(actions.updatePanelLayoutParticipants(selectedForLecture));
  if (meeting.mode === modes.LECTURE) {
    yield selectedForLecture.map((participantId) => mSocketSend(moderationActions.mute, participant._id,
      participantId, {
        audio: false,
        video: false
      }));
  }
}
/**
 * set meeting state received from MS
 */
function* meetingChangeMode({ payload }) {
  let acts = [];
  const participant = yield select(participantSelector);
  const participants = yield select(remoteParticipantsSelector);
  const geometry = yield getLayoutGeometry();
  acts.push(put(actions.updateLayoutGeometry(geometry)));
  Object.keys(payload.mutedState).forEach(key => {
    if (participant._id !== key) {
      if (JSON.stringify(participants[key].status.mute)
        !== JSON.stringify(payload.mutedState[key])) {
        acts.push(put(actions.participantMuteUpdate(key, payload.mutedState[
          key])));
      }
    } else {
      if (JSON.stringify(participant.status.mute)
        !== JSON.stringify(payload.mutedState[key])) {
        acts.push(put(actions.mute(payload.mutedState[key])));
      }
    }
  });
  yield all(acts);
}

function* meetingModeChanged({ payload }) {
  if (payload.state.mode === modes.LECTURE && payload.state.storyboardId) {
    const stb = yield select(storyboardStateSelector);
    if (stb.storyboard && stb.storyboard._id !== payload.state.storyboardId && stb
      .slidenumber !== 0) {
      yield put(actions.setSlideNumber(0));
    }
    yield put(apiActions.storyboardFetch(payload.state.storyboardId));
  }
  yield;
}

function* layoutChanged() {
  const geometry = yield getLayoutGeometry();
  yield put(actions.updateLayoutGeometry(geometry));
}

function* meetingSlideChanged({ payload: { number } }) {
  yield mSocketSend(moderationActions.setSlide, number);
}

function* lectureLayoutSaga() {
  yield takeLatest(types.SET_LECTURE_LAYOUT, updateLayout);
  yield takeLatest(types.MEETING_LAYOUT_MODE, updateLayout);
  yield takeLatest(types.CREATE_PARTICIPANT_SUCCESS, updateLayout);
  yield takeLatest(types.MEETING_CHANGE_LAYOUT, layoutChanged);
  yield takeLatest(types.MEETING_SET_MODE, meetingSetMode);
  yield takeLatest(types.MEETING_CHANGE_MODE, meetingChangeMode);
  yield takeLatest(types.MEETING_CHANGE_MODE, meetingModeChanged);
  yield takeLatest(types.SELECT_FOR_LECTURE, selectForLecture);
  yield takeLatest(types.SELECT_FOR_PANEL_MODE, selectForPanelMode);
  yield takeLatest(types.MEETING_SEND_LAYOUT, sendLayout);
  yield takeLatest(types.SET_MEETING_STATE, meetingModeChanged);
  yield takeLatest(types.MEETING_SLIDE_NUMBER, meetingSlideChanged);
}

function* muteSaga() {
  yield takeEvery(types.MUTE_ALL, function* ({ state }) {
    let muteState = yield select(muteStateSelector);
    if (state.audio) muteState.audio = Object.assign(muteState.audio, state
      .audio);
    if (state.video) muteState.video = Object.assign(muteState.video, state
      .video);
    yield put(actions.setMuteState(muteState));
    yield put(actions.muteStream());
    yield mSocketSend(moderationActions.muteUpdate, muteState);
  });
  yield takeEvery(types.MUTE_AUDIO, function* ({ state }) {
    const muteState = yield select(muteStateSelector);
    let newState;
    if (typeof state === typeof true) {
      newState = Object.assign(muteState, {
        audio: Object.assign(muteState.audio, { state })
      });
    } else {
      newState = Object.assign(muteState, {
        audio: Object.assign(muteState.audio, state)
      });
    }
    yield put(actions.mute(newState));
  });
  yield takeEvery(types.MUTE_VIDEO, function* ({ state }) {
    const muteState = yield select(muteStateSelector);
    let newState;
    if (typeof state === typeof true) {
      newState = Object.assign(muteState, {
        video: Object.assign(muteState.video, { state })
      });
    } else {
      newState = Object.assign(muteState, {
        video: Object.assign(muteState.video, state)
      });
    }
    yield put(actions.mute(newState));
  });
  yield takeEvery(types.REMOTE_MUTE, function* ({
    participantId: targetParticipantId,
    device,
    state
  }) {
    const participant = yield select(participantSelector);
    if (state) {
      yield mSocketSend(moderationActions.mute, participant._id,
        targetParticipantId, {
          [device]: state
        });
    } else {
      yield all([
        put(actions.unmuteRequestSent(targetParticipantId, device)),
        mSocketSend(moderationActions.unmuteRequest,
          participant._id, targetParticipantId, {
            [device]: state
          })
      ]);
    }
  });
  yield takeEvery(types.UNMUTE_RESPONSE, function* ({
    payload: {
      device,
      senderId
    }
  }) {
    const participant = yield select(participantSelector);
    if (device.hasOwnProperty(deviceTypes.AUDIO)) {
      if (device[deviceTypes.AUDIO]) {
        yield put(actions.muteAudio({ state: false, byModerator: false }));
      }
    }
    if (device.hasOwnProperty(deviceTypes.VIDEO)) {
      if (device[deviceTypes.VIDEO]) {
        yield put(actions.muteVideo({ state: false, byModerator: false }));
      }
    }
    yield mSocketSend(moderationActions.unmuteResponse,
      senderId,
      participant._id,
      device);
  });

  yield takeEvery(types.MUTE_STREAM, function* () {
    const participant = yield select(selectors.participantSelector);
    if (participant && participant.streams && participant.streams
      .mediaStreamId) {
      const streams = yield select(selectors.streamsSelector);
      if (streams) {
        const stream = streams[participant.streams.mediaStreamId];
        if (stream) {
          const { audio, video } = participant.status.mute;
          const isAudioMuted = !audio || audio.state || audio.byModerator;
          const isVideoMuted = !video || video.state || video.byModerator;
          if (stream.stream) {
            stream.stream.getTracks().forEach(track => {
              if (track.kind === 'audio') {
                track.enabled = !isAudioMuted;
              } else if (track.kind === 'video') {
                track.enabled = !isVideoMuted;
              }
            });
          }

          if (stream.hasAudio()) {
            stream.muteAudio(isAudioMuted, () => {
              if (stream.hasVideo()) stream.muteVideo(isVideoMuted);
            });
            if (stream.stream) {
              stream.stream.getAudioTracks().forEach(track => {
                track.enabled = !isAudioMuted;
              });
            }
            if (stream.hasVideo()) stream.muteVideo(isVideoMuted);
          }
        }
      }
    }
  });
}

function* meetingChatSaga() {
  yield takeEvery(types.SEND_MESSAGE,
    function* ({ payload }) {
      if (payload) {
        yield mSocketSend(moderationActions.chat, payload);
      }
    }
  );
}

function* meetingTranscriptSaga() {
  // transcript chat 
  yield takeEvery(types.SEND_TRANSCRIPT,
    function* ({ payload }) {
      if (payload) {
        yield mSocketSend(moderationActions.sendTranscript, payload);
      }
    }
  );
}
// TODO: maybe it worth to implement next logic in meetingTranscriptSaga
function* meetingSubtitlesSaga() {
  // subtitles' text
  yield takeEvery(types.SHARE_TRANSCRIPT,
    function* ({ payload }) {
      if (payload) {
        yield mSocketSend(moderationActions.shareTranscript, payload);
      }
    }
  );
}

function* bfmOnPausedSaga() {
  yield takeEvery(types.BFM_ON_PAUSE,
    function* ({ payload }) {
      yield mSocketSend(moderationActions.bfmOnPause, payload);
    });
}

function* meetingSubtitlesLangsSaga() {
  // transfer subtitles settings for each user
  yield takeEvery(types.TURN_ON_SUBTITLES,
    function* ({ payload }) {
      if (payload) {

        yield mSocketSend(moderationActions.turnOnSubtitles, payload);
      }
    });
}

function* requestModeratedAction({ action }) {
  const canModerate = yield select(canModerateSelector);
  if (canModerate) {
    yield put(action);
  } else {
    const hasOwnerPin = yield select(hasOwnerPinSelector);
    if (hasOwnerPin) { // does the room has owner pin?
      yield put(actions.requestOwnerPin(action));
    } else {
      // if user is not a moderator and there is not room's owner pin
      yield put(actions.SDKMessage(SDKMessages.PERMISSION_DENIED));
    }
  }
}

function* moderatedActionSaga() {
  yield takeEvery(userTypes.LOGIN_SUCCESS, function* () {
    const user = yield select(selectors.userCallOptionsSelector);
    yield mSocketSend(moderationActions.getRole, user.token);
  });

  yield takeEvery(userTypes.LOGOUT, function* () {
    yield mSocketSend(moderationActions.getRole, null);
  });

  yield takeEvery(types.SET_OWNER_PIN, function* ({ pin }) {
    yield mSocketSend(moderationActions.setOwnerPin, pin);
  });
  yield takeEvery(types.PARTICIPANT_NAME_CHANGED, function* ({ payload: { participantId, name } }) {
    const participant = yield select(selectors.participantSelector);
    if (participantId && participant._id === participantId) {
      yield mSocketSend(moderationActions.changeParticipantName, participantId, name);
    }
  });
  yield takeEvery(types.KICK_PARTICIPANT, function* ({ roomId, participantId }) {
    yield mSocketSend(moderationActions.kickParticipant,
      roomId,
      participantId);
  });
}

function* localStreamPublishedSaga() {
  yield takeEvery(types.LOCAL_STREAM_ADDED, function* ({ payload }) {
    if (payload) {
      yield put(actions.muteStream());
      yield mSocketSend(moderationActions.publishStream, payload.getID());
    }
  });
}

function* raiseHandSaga() {
  yield takeEvery(types.RAISE_HAND_REQ,
    function* ({ payload: state }) {
      yield mSocketSend(moderationActions.raiseHand, !!state);
    }
  );
}

function* raiseHandResponseSaga() {
  yield takeEvery(types.RAISE_HAND_RES,
    function* ({ payload }) {
      if (payload.accepted) {
        const participant = yield select(selectors.participantSelector);
        yield all([
          mSocketSend(moderationActions.raiseHandAccept, payload.id),
          // xxx: do it on target's side
          mSocketSend(moderationActions.mute, participant._id,
            payload.id, {
              audio: false,
              video: false
            })
        ]);
      } else {
        yield mSocketSend(moderationActions.raiseHandDecline, payload.id);
      }
    }
  );
}

function* screenStreamPublishedSaga() {
  yield takeEvery(types.SCREEN_SHARE_STARTED, function* ({ payload }) {
    if (payload) {
      yield put(actions.muteStream());
      yield mSocketSend(moderationActions.publishScreenStream, payload);
    }
  });
}

function* statsReceived({ payload }) {
  const participant = yield select(selectors.participantSelector);
  const remoteParticipants = yield select(selectors.remoteParticipantsSelector);
  for (const pId in payload) {
    if (payload.hasOwnProperty(pId)) {
      if (participant._id === pId && !participant.avatar) {
        yield put(actions.getAvatar(participant._id));
      } else if (remoteParticipants.hasOwnProperty(pId) && !remoteParticipants[pId].avatar) {
        yield put(actions.getAvatar(pId));
      }
    }
  }
}
function* statsSaga() {
  yield takeEvery(types.STATS_PARTICIPANT, statsReceived);
}

function* sipSaga() {
  yield takeEvery(types.SEND_SIP_STATE, sipState);
}

function* sipState({ payload: { sipUri, room, state } }) {
  yield put(actions.setSipState({ sipUri, room, state }));
  yield mSocketSend(moderationActions.sendToSip, sipUri, room, state);
}

function* participantAvatarSaga() {
  yield takeEvery(types.PARTICIPANT_AVATAR_GET, function* ({ participantId }) {
    const stream = yield select(selectors.participantStreamSelector, participantId);
    if (stream) {
      const avatar = generateThumbnail(stream);
      if (avatar) {
        yield put(actions.setAvatar(participantId, avatar));
      }
    }
  });
}

export function* moderationMessageHandler(channel) {
  yield takeEvery(types.CONNECTION_DISCONNECT, () => {
    channel.close();
    try {
      if (AndroidJSInterface) {
        AndroidJSInterface.disconnect();
      }
    } catch (e) {
      // console.log('Don`t Android');
    }
    // console.log('@ close moderation channel');
  });
  yield takeEvery(channel, function* (message) {
    // console.log('@ moderation =>', message);
    if (!message || (message.type !== 'web')) {
      console.error('Missing or malformed message');
      return;
    }
    // console.log('@moderationSaga ', message);
    let action = actions.noop();
    switch (message.data.method) {
      case methods.READY:
        action = actions.moderationServerReady();
        break;
      case methods.MEETING_STATE:
        action = actions.setMeetingState(message.data.params);
        break;
      case methods.MEETING_SET_MODE:
        action = actions.setMeetingMode(message.data.params);
        break;
      case methods.LECTURE_LAYOUT:
        action = actions.setMeetingLayout(message.data.params);
        break;
      case methods.LECTURE_SLIDE_NUMBER:
        action = actions.slideNumberChanged(message.data.params.number);
        break;
      case methods.PARTICIPANT_ADDED:
        action = actions.addParticipant(message.data.params);
        break;
      case methods.PARTICIPANT_PUBLISHED:
        action = actions.addParticipantStream(message.data.params);
        break;
      case methods.PARTICIPANT_NAME_CHANGED:
        action = actions.changeParticipantName(message.data.params.participantId, message.data.params.name);
        break;
      case methods.PARTICIPANT_REMOVED:
        action = actions.removeParticipant(message.data.params.participantId);
        break;
      case methods.DISCONNECT:
        action = actions.disconnect();
        break;
      case methods.MUTE_UPDATE:
        {
          const participant = yield select(participantSelector);
          if (participant._id !== message.data.params.participantId) {
            action = actions.participantMuteUpdate(
              message.data.params.participantId,
              message.data.params.mute);
          }
        }
        break;
      case methods.MUTE:
        const newState = message.data.params.state;
        if (newState.hasOwnProperty('audio')
          && newState.hasOwnProperty('video'))
          action = actions.mute({
            audio: { byModerator: newState.audio },
            video: { byModerator: newState.video }
          });
        else if (newState.hasOwnProperty('audio'))
          action = actions.muteAudio({ byModerator: newState.audio });
        else if (newState.hasOwnProperty('video'))
          action = actions.muteVideo({ byModerator: newState.video });
        break;
      case methods.UNMUTE_REQUEST:
        if (message.data.params.state.hasOwnProperty('audio'))
          action = actions.moderatorUnmuteRequest(message.data.params.senderId,
            'audio');
        if (message.data.params.state.hasOwnProperty('video'))
          action = actions.moderatorUnmuteRequest(message.data.params.senderId,
            'video');
        break;
      case methods.UNMUTE_RESPONSE:
        action = actions.moderatorUnmuteResponse(message.data.params.senderId,
          message.data.params.state);
        break;
      case methods.OWNER_PIN_SUCCESS:
        action = actions.ownerPinSuccess(
          message.data.params.participantId,
          message.data.params.role,
          message.data.params.isRoomOwner
        );
        break;
      case methods.OWNER_PIN_FAILED:
        action = actions.ownerPinFailed(message.data.params);
        break;
      case methods.ROLE_CHANGED:
        action = actions.roleChanged(
          message.data.params.participantId,
          message.data.params.role,
          message.data.params.isRoomOwner
        );
        break;

      case methods.CHAT:
        {
          const participant = yield select(participantSelector);
          if (participant._id !== message.data.params.authorId) {
            action = actions.addChatMessage(message.data.params);
          }
        }
        break;

      case methods.SEND_TRANSCRIPT:
        {
          const participant = yield select(participantSelector);
          if (participant._id !== message.data.params.authorId) {
            action = actions.addTranscriptMessage(message.data.params);
          }
        }
        break;

      case methods.SHARE_TRANSCRIPT:
        {
          const participant = yield select(participantSelector);
          if (participant._id !== message.data.params.participantId) {
            action = actions.receiveTranscript(message.data.params);
          }
        }
        break;

      case methods.TURN_ON_SUBTITLES:
        {
          const participant = yield select(participantSelector);
          if (participant._id !== message.data.params.participantId) {
            action = actions.receiveSubtitlesRequest(message.data.params);
          }
        }
        break;

      case methods.RAISE_HAND_UPDATE:
        {
          const participant = yield select(participantSelector);
          if (participant._id !== message.data.params.participantId) {
            action = actions.raiseHandUpdate(message.data.params);
          }
        }
        break;

      case methods.RAISE_HAND_RES:
        action = actions.raiseHandRequest(false);
        break;
      case methods.STREAMING_STATE:
        action = actions.setStreamingState(message.data.params.newState);
        break;
      case methods.PARTICIPANT_SHARES:
        action = actions.setRemoteScreenStream(message.data.params.streamId,
          message.data.params.participantId);
        break;
      case methods.SHARING_STOP:
        action = actions.stopSharing();
        break;
      case methods.STATS_UPDATE:
        action = actions.updateStats(message.data.params.stats);
        break;
      case methods.RECEIVE_REQUEST_TO_PARTICIPATE:
        action = actions.receivedRequestToParticipate(message);
        break;
      case methods.SIP_CALL_STATE:
        action = yield _sipStateChange(message.data.params);// actions.setSipState(message.data.params);
        break;

      case methods.BFM_ON_PAUSE:
        action = actions.setBfmPauseState(message.data.params);
        break;
      default:
        break;
    }
    yield put(action);
  });
}

/**
 * @param {Object} params 
 * @param {String} params.sipUri
 * @param {Number} params.state
 */
function* _sipStateChange(params) {
  const { sipUri, state, message } = params;
  const { sipUri: currentUri } = yield select(selectors.sipStateSelector);
  if (sipUri !== currentUri) return actions.noop();
  if (state === CallStates.ACCEPTED) yield put(apiActions.generalInfo('sip-call-accepted'));
  if (state === CallStates.REJECTED) {
    if (message) {
      yield put(apiActions.generalWarning({ message: 'sip-call-rejected-ext', args: { message } }));
    } else {
      yield put(apiActions.generalWarning('sip-call-rejected'));
    }
  }
  return actions.setSipState(params);
}

export default function* saga() {
  yield all([
    screenStreamPublishedSaga(),
    localStreamPublishedSaga(),
    lectureLayoutSaga(),
    muteSaga(),
    moderatedActionSaga(),
    meetingChatSaga(),
    meetingTranscriptSaga(),
    meetingSubtitlesSaga(),
    meetingSubtitlesLangsSaga(),
    raiseHandSaga(),
    raiseHandResponseSaga(),
    participantAvatarSaga(),
    statsSaga(),
    sipSaga(),
    bfmOnPausedSaga()
  ]);
}
