import { computeQoe, formatDelay, computeBitrate, formatBitrate } from './utils';
// TODO: review and refactor
export default class StreamStatsChrome {
  constructor(stream) {
    this.stream = stream;
  }

  getRawStats() {
    return new Promise((resolve, reject) => {
      if (!this.stream.pc)
        return reject('invalid stream');
      this.stream.pc.peerConnection.getStats((response) => {
        return resolve(response.result());
      });
    });
  }
  compute() {
    let p;
    if (this._rawStats) {
      this._prevRawStats = this._rawStats;
      p = Promise.resolve(true);
    } else {
      p = this.getRawStats()
        .then((s) => {
          new Promise((resolve) => {
            setTimeout(() => {
              this._prevRawStats = s;
              resolve(true);
            }, 500);
          });
        });
    }
    return p
      .then(() => this.getRawStats())
      .then((rawStats) => {
        this._rawStats = rawStats;
        let stats = []; // a collection of {label, direction, value, formatted}, where direction is rx or tx, and formatted is formatted value

        let rtt = extractStatAsInt(this._rawStats, 'ssrc', 'googRtt');

        if (rtt !== null) {
          stats.push({
            label: 'QOE',
            name: 'stats-qoe',
            value: computeQoe(rtt)
          });
          stats.push({
            label: 'RTT',
            name: 'stats-rtt',
            value: rtt,
            formatted: formatDelay(rtt)
          });
        }

        if (this.stream.simulcastLayers && this.stream.simulcastLayers > 1)
          stats.push({
            label: 'Simulcast Layers',
            name: 'stats-simulcast-layers',
            direction: 'tx',
            value: this.stream.simulcastLayers
          });

        let txAudio = getStatsReport(this._rawStats, 'ssrc',
          'audioInputLevel');
        let txPrevAudio = getStatsReport(this._prevRawStats, 'ssrc',
          'audioInputLevel');
        if (txAudio) {
          let txAudioCodec = txAudio.stat('googCodecName');
          stats.push({
            label: 'Audio Codec',
            name: 'stats-audio-codec',
            direction: 'tx',
            value: txAudioCodec
          });
          let txAudioBitrate = computeBitrate(txAudio, txPrevAudio,
            'bytesSent');
          if (txAudioBitrate > 0)
            stats.push({
              label: 'Audio BW',
              name: 'stats-audio-bw',
              direction: 'tx',
              value: txAudioBitrate,
              formatted: formatBitrate(txAudioBitrate)
            });
        }

        let rxAudio = getStatsReport(this._rawStats, 'ssrc',
          'audioOutputLevel');
        let rxPrevAudio = getStatsReport(this._prevRawStats, 'ssrc',
          'audioOutputLevel');
        if (rxAudio) {
          let rxAudioCodec = rxAudio.stat('googCodecName');
          stats.push({
            label: 'Audio Codec',
            name: 'stats-audio-codec',
            direction: 'rx',
            value: rxAudioCodec
          });
          let rxAudioBitrate = computeBitrate(rxAudio, rxPrevAudio,
            'bytesReceived');
          if (rxAudioBitrate > 0)
            stats.push({
              label: 'Audio BW',
              name: 'stats-audio-bw',
              direction: 'rx',
              value: rxAudioBitrate,
              formatted: formatBitrate(rxAudioBitrate)
            });
          let rxAudioDelay = rxAudio.stat('googCurrentDelayMs');
          stats.push({
            label: 'Audio Delay',
            name: 'stats-audio-delay',
            direction: 'rx',
            value: rxAudioDelay,
            formatted: formatDelay(
              rxAudioDelay)
          });
        }

        let txVideo = getStatsReport(this._rawStats, 'ssrc',
          'googFirsReceived');
        let txPrevVideo = getStatsReport(this._prevRawStats, 'ssrc',
          'googFirsReceived');
        if (txVideo) {
          let txVideoCodec = txVideo.stat('googCodecName');
          stats.push({
            label: 'Video Codec',
            name: 'stats-video-codec',
            direction: 'tx',
            value: txVideoCodec
          });
          let txVideoHeight = txVideo.stat('googFrameHeightSent');
          let txVideoFps = txVideo.stat('googFrameRateSent');
          stats.push({
            label: 'Video Resolution',
            name: 'stats-video-resolution',
            direction: 'tx',
            value: txVideoHeight + 'p' + txVideoFps
          });
          let txVideoBitrate = computeBitrate(txVideo, txPrevVideo,
            'bytesSent');
          if (txVideoBitrate > 0)
            stats.push({
              label: 'Video BW',
              name: 'stats-video-bw',
              direction: 'tx',
              value: txVideoBitrate,
              formatted: formatBitrate(txVideoBitrate)
            });
        }

        let rxVideo = getStatsReport(this._rawStats, 'ssrc', 'googFirsSent');
        let rxPrevVideo = getStatsReport(this._prevRawStats, 'ssrc',
          'googFirsSent');
        if (rxVideo) {
          let rxVideoCodec = rxVideo.stat('googCodecName');
          stats.push({
            label: 'Video Codec',
            name: 'stats-video-codec',
            direction: 'rx',
            value: rxVideoCodec
          });

          let rxVideoHeight = rxVideo.stat('googFrameHeightReceived');
          let rxVideoFps = rxVideo.stat('googFrameRateDecoded');
          stats.push({
            label: 'Video Resolution',
            name: 'stats-video-resolution',
            direction: 'rx',
            value: rxVideoHeight + 'p' + rxVideoFps
          });
          let rxVideoBitrate = computeBitrate(rxVideo, rxPrevVideo,
            'bytesReceived');
          if (rxVideoBitrate > 0)
            stats.push({
              label: 'Video BW',
              name: 'stats-video-bw',
              direction: 'rx',
              value: rxVideoBitrate,
              formatted: formatBitrate(
                rxVideoBitrate)
            });
          let rxVideoDelay = rxVideo.stat('googCurrentDelayMs');
          stats.push({
            label: 'Video Delay',
            name: 'stats-video-delay',
            direction: 'rx',
            value: rxVideoDelay,
            formatted: formatDelay(rxVideoDelay)
          });
        }
        return stats;
      });
  };
}

// Return the integer stat |statName| from the object with type |statObj| in
// |stats|, or null if not present.
function extractStatAsInt(stats, statObj, statName) {
  // Ignore stats that have a 'null-ish' value.
  // The correct fix is indicated in
  // https://code.google.com/p/webrtc/issues/detail?id=3377.
  const str = extractStat(stats, statObj, statName);
  if (str) {
    let val = parseInt(str);
    if (val !== -1)
      return val;
  }
  return null;
}

// Return the stat |statName| from the object with type |statObj| in |stats|
// as a string, or null if not present.
function extractStat(stats, statObj, statName) {
  let report = getStatsReport(stats, statObj, statName);
  if (report && report.names().indexOf(statName) !== -1)
    return report.stat(statName);
  return null;
}

// Return the stats report with type |statObj| in |stats|, with the stat
// |statName| (if specified), and value |statVal| (if specified). Return
// undef if not present.
function getStatsReport(stats, statObj, statName, statVal) {
  if (stats) {
    for (let i = 0; i < stats.length; ++i) {
      let report = stats[i];

      if (report.type === statObj) {
        let found = true;
        // If |statName| is present, ensure |report| has that stat.
        // If |statVal| is present, ensure the value matches.

        if (statName) {
          let val = statName === 'id' ? report.id : report.stat(statName);
          found = (statVal !== undefined) ? (val === statVal) : val;
        }
        if (found)
          return report;
      }
    }
  }
}
