import types from './types';

export default {
  setServerUrl: (url) => ({
    type: types.SET_SERVER_URL,
    payload: url
  }),
  updateToken: (token) => ({
    type: types.UPDATE_TOKEN,
    payload: token
  }),
  login: (username, password) => ({
    type: types.LOGIN,
    payload: {
      username,
      password
    }
  }),
  loginSuccess: (profile, token) => ({
    type: types.LOGIN_SUCCESS,
    payload: {
      profile,
      token
    }
  }),
  setProfile: profile => ({ type: types.USER_SET_PROFILE, payload: profile }),
  refreshProfile: () => ({ type: types.USER_REFRESH_PROFILE }),

  loginError: (error, noRedirect) => ({
    type: types.LOGIN_ERROR,
    payload: error,
    noRedirect: noRedirect
  }),
  logout: () => ({ type: types.LOGOUT }),
  editProfile: profile => ({ type: types.USER_EDIT_PROFILE, profile }),
  editProfileError: (error) => ({ type: types.USER_EDIT_PROFILE_ERROR, error }), // ?,
  setEventRoom: roomId => ({ type: types.SET_EVENT_ROOM, payload: roomId })
};
