import {
  all,
  put,
  takeEvery,
  select
} from 'redux-saga/effects';
import { eventChannel } from 'redux-saga';
import actions from './actions';
import meetingActions from '../meeting/actions';
import types, { methods } from './types';
import {
  serverUrlSelector,
  userTokenSelector,
  userCallOptionsSelector
} from '../meeting/selectors';
import moderationActions from '../moderationActions';
import { mSocketSend } from '../utils';

const socketSelector = (state) => state.teevid.viewer.moderationSocket;
const participantSelector = (state) => state.teevid.viewer.participant;

function* mViewerSocketSend(moderationAction, ...args) {
  const mSocket = yield select(socketSelector);
  const { token } = yield select(userCallOptionsSelector);
  if (mSocket && mSocket.readyState === 1)
    mSocket.send(JSON.stringify(moderationAction(token, ...args)));
}

function createViewerChannel(moderationSocket) {
  return eventChannel((emit) => {
    const messageHandler = (socketEvent) => {
      let message = socketEvent.data;
      // console.log('socket data ', socketEvent);

      if (message && message === '_') { // this client is alive
        if (moderationSocket && moderationSocket.readyState === 1) {
          // console.log('Viewer message ', message);
          moderationSocket.send(message);
        }
      } else {
        message = JSON.parse(message);
        // console.log('@ message', message);
        emit(message);
      }
    };

    moderationSocket.onerror = (error) => {
      console.log(error);
    };

    moderationSocket.onmessage = messageHandler;
    return () => { moderationSocket.close(); };
  });
}
// saga 1 
function* prepareForConnection() {
  yield takeEvery(types.OPEN_VIEWER_CHANNEL, function* ({ payload: { roomId } }) {
    const serverUrl = yield select(serverUrlSelector);
    const token = yield select(userTokenSelector);
    const socketUrl =
      `wss://${serverUrl}/socket/moderation?token=${encodeURIComponent(token)}&roomId=${roomId}`;

    const moderationSocket = new WebSocket(socketUrl);
    moderationSocket.onclose = (reason) => {
      console.info('Moderation Socket closed', reason);
      // TeeVidSdk.actions.disconnect();
    };

    const viewerChannel = createViewerChannel(moderationSocket);
    yield put(actions.viewerChannelOpened(moderationSocket));

    yield viewerChannelEventHandler(viewerChannel);
  });
}

function* viewerChannelEventHandler(channel) {
  yield takeEvery(types.CLOSE_VIEWER_CHANNEL, () => {
    channel.close();
  });
  yield takeEvery(channel, function* (message) {
    const { method, params } = message.data;
    let action = actions.noop();
    // console.log('@ type ', methods.REQUEST_TO_PARTICIPATE_SENT , method);
    switch (method) {
      case methods.ACCEPT_REQUEST_TO_PARTICIPATE:
        action = actions.requestToParticipateAccepted(params);
        break;

      case methods.REJECT_REQUEST_TO_PARTICIPATE:
        action = actions.requestToParticipateRejected(params);
        break;

      case methods.REQUEST_TO_PARTICIPATE_SENT:
        action = actions.requestToParticipateSent(params);
        break;

      case methods.SET_STREAMING_STATE: 
        action = actions.setReceivedStreamingState(params);
        break;

      case methods.MEETING_STATE: 
        action = actions.setMeetingState(params);
        break;

      default:
        break;
    }
    yield put(action);
  });
}

function* requestToParticipateSaga() {
  yield takeEvery(types.REQUEST_TO_PARTICIPATE,
    function* ({ payload }) {
      if (payload) {
        yield mViewerSocketSend(moderationActions.sendRequestToParticipate, payload);
      }
      yield;
    });
}

function* viewerLeavesPlaybackPage () {
  yield takeEvery(types.VIEWER_LEAVES_PLAYBACK_PAGE,
    function* ({ payload }) {
      if(payload) {
        // console.log('VIEWER_LEAVES_PLAYBACK_PAGE ', payload );
        yield mViewerSocketSend(moderationActions.leavePlaybackPage, payload);
      }
    });
}

function* acceptRequestToParticipateSaga() {
  yield takeEvery(types.ACCEPT_REQUEST_TO_PARTICIPATE,
    function* ({ payload }) {
      if (payload) {
        yield mSocketSend(moderationActions.acceptRequestToParticipate, { id: payload });
      }
    });
}

function* rejectRequestToParticipateSaga() {
  yield takeEvery(types.REJECT_REQUEST_TO_PARTICIPATE,
    function* ({ payload }) {
      if (payload) {
        yield mSocketSend(moderationActions.rejectRequestToParticipate, { id: payload });
      }
    });
}

function* joinToMeetingSaga() {
  yield takeEvery(types.JOIN_TO_MEETING,
    function* () {
      const mSocket = yield select(socketSelector);
      const participant = yield select(participantSelector);
      yield put(meetingActions.setCurrentRoom(participant.room));
      yield put(meetingActions.participantCreated(participant));
      if (mSocket && mSocket.readyState !== 3)
        mSocket.close();

    });
}

export default function* saga() {
  yield all([
    prepareForConnection(),
    requestToParticipateSaga(),
    acceptRequestToParticipateSaga(),
    rejectRequestToParticipateSaga(),
    joinToMeetingSaga(),
    viewerLeavesPlaybackPage()
  ]);
}