import actions from './actions';
import moderationActions from '../moderationActions';
import { apiCallHelper } from '../utils';

export function* licenseSaga() {
  yield takeEvery(types.LICENSE_GET, function* (action) {
    try {
      let token = null;
      const { token:t } = yield select(userCallOptionsSelector);
      if(t) {
        token = t;
      } else {
        token = yield select(tokenSelector);
      }

      if (token) {
        const data = yield TeeVidSdk.license.get();
        yield put(actions.licenseSuccess(data));
      } 
    } catch (error) {
      yield put(actions.generalError(error));
    }
    yield;
  });
  yield takeEvery(types.LICENSE_UPDATE, function* ({ license }) {
    try {
      const { token } = yield select(userCallOptionsSelector);
      if (token) {
        const data = yield TeeVidSdk.license.update(license);
        yield put(actions.licenseUpdateSuccess(data));
      }
    } catch (error) {
      yield put(actions.generalError(error));
    }
    yield;
  });
}

