export function* fetchStoryboard({ stbId }) {
  try {
    const { token: userToken, serverUrl } = yield select(userCallOptionsSelector);
    const participantToken = yield select(tokenSelector);
    const token = userToken || participantToken;
    if (token) {
      const { payload: data } = yield apiCallHelper(serverUrl, token, 'get', `/storyboard/${stbId}`);
      yield put(actions.storyboardFetchSuccess(data));
    }
  } catch (error) {
    yield put(actions.storyboardError(error));
  }
}

