export function* adminPortalSaga() {
  yield takeEvery(types.SET_ADMIN_PORTAL, function* ({ payload }) {
    if (payload) {
      const { token } = yield select(userCallOptionsSelector);
      const serverUrl = yield select(serverUrlSelector);
      const socketUrl =
        `wss://${serverUrl}/socket/moderation?token=${encodeURIComponent(token)}`;

      const adminSocket = new WebSocket(socketUrl);
      const adminChannel = createChannel(adminSocket);
      yield put(actions.adminSocket(adminSocket));
      yield fork(adminMessageHandler, adminChannel);
    } else {
      const socket = yield select((state) => state.teevid.api.adminSocket);
      if (socket && (socket.readyState === 1)) {
        socket.close();
      }
      yield put(actions.adminSocket(null));
    }
  });
  yield takeEvery(types.DELETE_FROM_ROOM, function* ({ roomId, participantId }) {
    yield sendSocketMessage(moderationActions.kickParticipant, roomId, participantId);
  });
  yield takeEvery(types.ROOM_CLOSE, function* ({ roomId }) {
    yield sendSocketMessage(moderationActions.roomClose, roomId);
  });
}

function* sendSocketMessage(method, ...params) {
  const socket = yield select((state) => state.teevid.api.adminSocket);
  const { token } = yield select(userCallOptionsSelector);
  if (socket && (socket.readyState === 1)) {
    if (method && (typeof method === 'function')) {
      const message = method(token, ...params);
      socket.send(JSON.stringify(message));
    }
  }
}

function createChannel(adminSocket) {
  return eventChannel((emit) => {
    const messageHandler = (socketEvent) => {
      let message = socketEvent.data;
      if (message === '_') { // this client is alive
        adminSocket.send(message);
      } else {
        message = JSON.parse(message);
        // console.log('@ message', message);
        emit(message);
      }
    };
    adminSocket.onerror = (error) => {
      emit(error);
    };
    adminSocket.onmessage = messageHandler;
    return () => { adminSocket.close(); };
  });
}

function* adminMessageHandler(channel) {
  yield takeEvery(channel, function* (message) {
    // console.log('@ admin event', message);
    if (!message || (message.type !== 'middleware')) {
      console.error('Missing or malformed message');
      return;
    }
    let action = actions.noop();
    switch (message.data.method) {
      case AdminEvents.SERVER_STATUS:
        action = actions.serverStatus(message.data.params.status);
        break;
      case AdminEvents.MCU_INSTANCES:
        action = actions.mcuStats(message.data.params.instances);
        break;
      case AdminEvents.STATS_UPDATED:
        action = actions.participantStats(message.data.params);
        break;
      default:
        break;
    }
    yield put(action);
  });
}

export function* getCurrentDevicesSaga() {
  yield takeEvery(types.REFRESH_CURRENT_MEDIA_DEVICES,
    function* ({ payload: options }) {
      if (navigator.mediaDevices
        && navigator.mediaDevices.enumerateDevices
        && (options.video || options.audio) && false) {
        const currentDevices = yield navigator.mediaDevices
          .getUserMedia(options)
          .then(function (stream) {
            const tracks = stream.getTracks();
            return tracks.reduce((acc, track) => {
              if (track.kind === 'audio' && (typeof track.getSettings
                === 'function')) {
                acc.audio = track.getSettings()
                  .deviceId;
              }
              if (track.kind === 'video' && (typeof track.getSettings
                === 'function')) {
                acc.video = track.getSettings()
                  .deviceId;
              }
              track.stop();
              return acc;
            }, { audio: null, video: null });
          })
          .catch(err => {
            console.log('===>', err);
          });
        yield put(actions.setCurrentMediaDevices(currentDevices));
      } else {
        yield put(actions.setCurrentMediaDevices(null));
      }
    });
}

export function* initDevicesSaga() {
  yield takeEvery(types.REFRESH_MEDIA_DEVICES, function* () {
    // ! this option is experimental and isn't available everywhere
    if (navigator.mediaDevices && navigator.mediaDevices.enumerateDevices) {

      const devices = yield navigator.mediaDevices.enumerateDevices()
        .then(devices => {
          const audio = devices.filter(d => d.kind === 'audioinput');
          const video = devices.filter(d => d.kind === 'videoinput');
          return { audio, video };
        });
      yield put(actions.setMediaDevices(devices));
    } else {
      yield put(actions.setMediaDevices({ audio: [], video: [] }));
    }
  });
}

