import types from './types';
// import { TypeScriptSource } from 'typedoc/dist/lib/utils/options/sources';

export default {
  setCurrentRoom: (room) => ({ type: types.SET_CURRENT_ROOM, payload: room }),

  roomStats: (stats) => ({ type: types.STATS_ROOM, payload: stats }),

  // =============== Participant ==============================
  createParticipant: (room, username, pin = {}) => ({
    type: types.CREATE_PARTICIPANT,
    payload: {
      room,
      username,
      pin
    }
  }),
  participantCreated: (participant) => ({
    type: types.CREATE_PARTICIPANT_SUCCESS,
    payload: participant
  }),
  participantCreationError: (error, state = 0) => ({
    type: types.CREATE_PARTICIPANT_ERROR,
    payload: {
      error,
      state
    }
  }),
  addParticipant: (participant) => ({
    type: types.CONNECTION_ADD_PARTICIPANT,
    payload: participant
  }),
  addParticipantStream: (message) => ({
    type: types.CONNECTION_ADD_PARTICIPANT_STREAM,
    payload: message
  }),
  removeParticipant: (participantId) => ({
    type: types.CONNECTION_PARTICIPANT_REMOVED,
    payload: participantId
  }),
  kickParticipant: (roomId, participantId) => ({
    type: types.KICK_PARTICIPANT,
    roomId,
    participantId
  }),
  // ================== Connection ===========================
  updateStats: (stats) => ({ type: types.STATS_PARTICIPANT, payload: stats }),
  connect: () => ({ type: types.CONNECT }),
  createConnection: (participant) => ({
    type: types.CREATE_CONNECTION,
    payload: participant
  }),
  connectionCreated: (connection) => ({
    type: types.CREATE_CONNECTION_SUCCESS,
    payload: { connection }
  }),
  connectionCreationError: (error) => ({
    type: types.CREATE_CONNECTION_ERROR,
    payload: error
  }),

  connectionSuccess: (connection) => ({
    type: types.CONNECTION_SUCCESS,
    connection
  }),
  connectionEvent: (event, type) => ({
    type: types.CONNECTION_EVENT,
    payload: {
      event,
      eventType: type
    }
  }),
  setOwnerPin: (pin) => ({ type: types.SET_OWNER_PIN, pin }),
  requestOwnerPin: (action) => ({
    type: types.REQUEST_OWNER_PIN,
    payload: { action }
  }),
  setConnectionState: (state = 0) => ({
    type: types.SET_CONNECTION_STATE,
    payload: state
  }),
  setAccessPinRequirement: (state) => ({
    type: types.SET_ACCESS_PIN_REQ,
    payload: state
  }),
  connectionCancel: () => ({ type: types.CONNECTION_CANCEL }),
  // connectionReady: (moderationSocket) => ({ type: types.CONNECTION_READY, payload: moderationSocket }),
  connectionReady: () => ({ type: types.CONNECTION_READY }),
  moderationSocketCreated: (moderationSocket) => ({
    type: types.MODERATION_SOCKET_CREATED,
    payload: moderationSocket
  }),
  moderationServerReady: () => ({ type: types.MODERATION_SERVER_READY }),
  connectionError: (error) => ({ type: types.CONNECTION_ERROR, payload: error }),
  connectionRoomReady: () => ({ type: types.ROOM_READY }),
  disconnect: () => ({ type: types.CONNECTION_DISCONNECT }),
  disconnected: () => ({ type: types.CONNECTION_DISCONNECTED }),
  // ==================== Streams =========================
  sendSipState: (state) => ({ type: types.SEND_SIP_STATE, payload: state }),
  setSipState: (state) => ({ type: types.SET_SIP_STATE, payload: state }),
  statusChanged: (status) => ({ type: types.STATUS_UPDATE, payload: status }),
  localStreamAdded: (stream) => ({
    type: types.LOCAL_STREAM_ADDED,
    payload: stream
  }),
  streamAdded: (stream) => ({
    type: types.CONNECTION_STREAM_ADDED,
    payload: stream
  }),
  streamRemoved: (stream) => ({
    type: types.CONNECTION_STREAM_REMOVED,
    payload: stream
  }),
  streamSubscribed: (stream) => ({
    type: types.CONNECTION_STREAM_SUBSCRIBED,
    payload: stream
  }),
  stopSharing: () => ({ type: types.CONNECTION_STOP_SHARING }),
  startSharing: () => ({ type: types.CONNECTION_START_SHARING }),
  // ? ==================== Moderation socket =========================
  mMessage: (message) => ({
    type: types.MODERATION_MESSAGE_RECEIVED,
    payload: message
  }),
  mMessageError: (error) => ({
    type: types.MODERATION_MESSAGE_ERROR,
    payload: error
  }),
  mSocketClosed: (reason) => ({
    type: types.MODERATION_SOCKET_CLOSED,
    payload: reason
  }),
  mSocketError: (error) => ({ type: types.MODERATION_SOCKET_ERROR, payload: error }),

  noop: () => ({ type: types.NOOP }),
  setMeetingState: (meetingState) => ({
    type: types.SET_MEETING_STATE,
    payload: meetingState
  }),
  setMeetingMode: (meetingMode) => ({
    type: types.MEETING_CHANGE_MODE,
    payload: meetingMode
  }),
  setMeetingLayout: (meetingState) => ({
    type: types.MEETING_CHANGE_LAYOUT,
    payload: meetingState
  }),
  setLayoutMode: (layoutMode) => ({
    type: types.MEETING_LAYOUT_MODE,
    payload: { layoutMode }
  }),
  setSlideNumber: (number) => ({
    type: types.MEETING_SLIDE_NUMBER,
    payload: { number }
  }),
  slideNumberChanged: (number) => ({
    type: types.MEETING_SLIDE_CHANGED,
    payload: { number }
  }),
  sendMeetingLayout: () => ({ type: types.MEETING_SEND_LAYOUT }),
  roomConnected: () => ({ type: types.ROOM_CONNECTED }),
  themesSuccessAll: themes => ({ type: types.THEMES_SUCCESS_ALL, themes }),
  themesFetchAll: () => ({ type: types.THEMES_FETCH }),
  // ===================== Connection events ============================

  raiseHandRequest: state => ({ type: types.RAISE_HAND_REQ, payload: state }),
  raiseHandUpdate: data => ({ type: types.RAISE_HAND_UPDATE, payload: data }),
  raiseHandResponse: (accepted, targetId) => ({
    type: types.RAISE_HAND_RES,
    payload: { accepted, id: targetId }
  }),

  setStreamingState: (state) => ({
    type: types.SET_STREAMING_STATE,
    payload: state
  }),
  startStreaming: () => ({ type: types.STREAMING_START }),
  stopStreaming: () => ({ type: types.STREAMING_STOP }),
  // ------------------------- Screen share --------------------
  setSharingStream: (streamId) => ({
    type: types.SCREEN_SHARE_STARTED,
    payload: streamId
  }),
  setRemoteScreenStream: (streamId, participantId) => ({
    type: types.SCREEN_SHARE_SET_REMOTE,
    payload: { streamId, participantId }
  }),
  removeSharingStream: (streamId) => ({
    type: types.SCREEN_SHARE_STOPPED,
    payload: streamId
  }),
  // ! ======================= Old stuff ========================

  getAvatar: (participantId) => ({
    type: types.PARTICIPANT_AVATAR_GET,
    participantId
  }),

  setAvatar: (participantId, avatar) => ({
    type: types.PARTICIPANT_AVATAR_SET,
    payload: {
      participantId,
      avatar
    }
  }),

  roleChanged: (participantId, role, isRoomOwner) => ({
    type: types.ROLE_CHANGED,
    payload: {
      participantId: participantId,
      data: {
        role: role,
        isRoomOwner: isRoomOwner
      }
    }
  }),
  ownerPinSuccess: (participantId, role, isRoomOwner) => ({
    type: types.OWNER_PIN_SUCCESS,
    payload: {
      participantId: participantId,
      data: {
        role: role,
        isRoomOwner: isRoomOwner
      }
    }
  }),
  ownerPinFailed: (data) => ({
    type: types.OWNER_PIN_FAILED,
    payload: data
  }),
  participantMuteUpdate: (participantId, mute) =>
    ({
      type: types.MUTE_UPDATED,
      payload: {
        participantId: participantId,
        mute
      }
    }),
  muteStream: () => ({ type: types.MUTE_STREAM }),
  mute: state => ({ type: types.MUTE_ALL, state }),
  muteAudio: state => ({ type: types.MUTE_AUDIO, state }),
  muteVideo: state => ({ type: types.MUTE_VIDEO, state }),
  moderatorUnmuteRequest: (senderId, device) => ({
    type: types.MODERATOR_UNMUTE_REQUEST,
    payload: { senderId, device }
  }),
  moderatorUnmuteResponse: (senderId, device) => ({
    type: types.MODERATOR_UNMUTE_RESPONSE,
    payload: { senderId, device }
  }),
  remoteMute: (participantId, device, state) => ({
    type: types.REMOTE_MUTE,
    participantId,
    device,
    state
  }),
  setMuteState: mute => ({ type: types.SET_MUTE_STATE, payload: mute }),
  /* moderator send request */
  unmuteRequestSent: (participantId, device) => ({
    type: types.UNMUTE_REQUEST_SENT,
    payload: { participantId, device }
  }),
  /*participant accept request*/
  setParticipantUnmuteRequest: (senderId, request) => ({
    type: types.PARTICIPANT_UNMUTE_REQUEST,
    senderId,
    request
  }),
  unmuteResponse: (senderId, device) => ({
    type: types.UNMUTE_RESPONSE,
    payload: {
      device,
      senderId
    }
  }),
  /*participant request accepted moderator event*/
  unmuteRequestAccepted: (participantId, request) => ({
    type: types.UNMUTE_REQUEST_ACCEPTED,
    participantId,
    request
  }),
  /*participant request declined moderator event*/
  unmuteRequestDeclined: (participantId, request) => ({
    type: types.UNMUTE_REQUEST_DECLINED,
    participantId,
    request
  }),
  setVideoStateIndication: state => ({
    type: types.MUTE_VIDEO_STATE_INDICATION,
    state
  }),
  setAudioStateIndication: state => ({
    type: types.MUTE_AUDIO_STATE_INDICATION,
    state
  }),
  setChatDisabled: state => ({ type: types.CHAT_DISABLE, state }),
  setScreenSharing: state => ({ type: types.SET_SCREEN_SHARE, state }),
  /**
   * @param {MediaDeviceInfo[]} devices
   */
  setAudioDevices: devices => ({
    type: types.SET_AUDIO_DEVICES,
    devices
  }),
  /**
   * @param {MediaDeviceInfo[]} devices
   */
  setVideoDevices: devices => ({
    type: types.SET_VIDEO_DEVICES,
    devices
  }),
  setCurrentDevices: (video, audio) => ({
    type: types.SET_CURRENT_DEVICES,
    devices: { video, audio }
  }),
  switchCurrentDevices: (video, audio) => ({
    type: types.SWITCH_CURRENT_DEVICES,
    video,
    audio
  }),
  initMediaDevices: (options) => ({ type: types.INIT_DEVICES, options }),
  // =============================================
  sendMessage: data => ({
    type: types.SEND_MESSAGE,
    payload: data
  }),
  addChatMessage: data => ({
    type: types.ADD_CHAT_MESSAGE,
    payload: data
  }),

  sendTranscript: data => ({
    type: types.SEND_TRANSCRIPT,
    payload: data
  }),
  addTranscriptMessage: data => ({
    type: types.ADD_TRANSCRIPT_MESSAGE,
    payload: data
  }),

  turnOnSubtitles: data => ({ // DIRECT FUNCTION FOR A RECEIVE_SUBTITLES_REQUEST
    type: types.TURN_ON_SUBTITLES,
    payload: data
  }),
  receiveSubtitlesRequest: data => ({
    type: types.RECEIVE_SUBTITLES_REQUEST,
    payload: data
  }),

  updateTranscript: data => ({ // has similar name to local action
    type: types.UPDATE_TRANSCRIPT,
    payload: data
  }),

  shareTranscript: data => ({ // DIRECT FUNCTION FOR RECEIVE_TRANSCRIPT
    type: types.SHARE_TRANSCRIPT,
    payload: data
  }),
  receiveTranscript: data => ({ // REVERSE FUNCTION FOR SHARE_TRANSCRIPT
    type: types.RECEIVE_TRANSCRIPT,
    payload: data
  }),
  bfmOnPause: state => ({
    type: types.BFM_ON_PAUSE,
    payload: state
  }),
  setBfmPauseState: state => ({
    type: types.SET_BFM_PAUSE_STATE,
    payload: state
  }),
  // ============================================================================
  applyLectureLayout: () => ({ type: types.MEETING_LECTURE_LAYOUT_APPLY }),

  setLectureLayout: layout => ({
    type: types.SET_LECTURE_LAYOUT,
    payload: {
      lectureLayout: layout
    }
  }),
  updateLayoutGeometry: (geometry) => ({
    type: types.UPDATE_LAYOUT_GEOMETRY,
    payload: { geometry }
  }),
  updateLayoutParticipants: (selectedForLecture) => ({
    type: types.UPDATE_LAYOUT_PARTICIPANTS,
    payload: { selectedForLecture }
  }),
  updatePanelLayoutParticipants: (selectedForLecture) => ({
    type: types.UPDATE_PANEL_LAYOUT_PARTICIPANTS,
    payload: { selectedForLecture }
  }),
  selectForLecture: (participantId, tileId, previousParticipantId) => ({
    type: types.SELECT_FOR_LECTURE,
    payload: { participantId, tileId, previousParticipantId }
  }),
  selectForPanelMode: (selectedForLecture) => ({
    type: types.SELECT_FOR_PANEL_MODE,
    payload: { selectedForLecture }
  }),
  setMutedStatus: (streamId, add) => ({
    type: types.PARTICIPANT_MUTED,
    streamId,
    add
  }),

  changeParticipantName: (participantId, name) => ({
    type: types.PARTICIPANT_NAME_CHANGED,
    payload: {
      participantId,
      name
    }
  }),
  setSelectionForLecture: partIds => ({
    type: types.MEETING_LECTURE_SET_SELECTION,
    list: partIds
  }),
  lectureLayoutUpdate: action => ({
    type: types.MEETING_LECTURE_LAYOUT_UPDATE,
    action
  }),
  selectParticipantForLecture: (id, index) => ({
    type: types.MEETING_LECTURE_SELECT,
    id,
    index
  }),
  requestModeratedAction: action => ({
    type: types.REQUEST_MODERATED_ACTION,
    action
  }),
  updateRole: (action, pin) => ({
    type: types.PARTICIPANT_ROLE_UPDATE,
    action,
    pin
  }),

  updateRoleSuccess: () => ({ type: types.PARTICIPANT_ROLE_UPDATE_SUCCESS }),
  updateRoleError: error => ({
    type: types.PARTICIPANT_ROLE_UPDATE_ERROR,
    payload: error
  }),
  modeSetup: options => ({ type: types.MEETING_MODE_SETUP, payload: options }),
  setSlide: slideNumber => ({ type: types.MEETING_SET_SLIDE, slideNumber }),
  setMode: (mode, stbId = null) => ({
    type: types.MEETING_SET_MODE,
    payload: {
      mode,
      stbId
    }
  }),
  setModeRequest: (mode) => ({ type: types.MEETING_SET_MODE_REQUEST, mode }),
  setPresenterStream: streamId => ({
    type: types.MEETING_SET_PRESENTER_STREAM,
    streamId
  }),
  setInitStb: stbId => ({ type: types.MEETING_INIT_STB, payload: stbId }),
  setScreenStream: stream => ({ type: types.MEETING_SET_SCREEN_STREAM, stream }),
  // ============================ Stats =============================
  setQoe: (participantId, qoe) => ({ type: types.STATS_QOE, participantId, qoe }),
  SDKMessage: (event) => ({ type: types.SDK_MESSAGE, payload: event }),

  // ============================= playback ===========================

  receivedRequestToParticipate: data => ({ type: types.RECEIVED_REQUEST_TO_PARTICIPATE, payload: data })
};
