import types from './types';
import userTypes from '../user/types';
import update from 'immutability-helper';
import { lectureLayouts } from '../constants';
const initialState = {
  adminSocket: null,
  isAdminPortal: false,
  users: [],
  license: {},
  servers: [],
  mcus: [],
  mediaServers: [],
  kurentoServers: [],
  liveMeetings: {},
  terminatedMeetings: {},
  serverUrl: '',
  storyboards: [],
  storyboard: null,
  rooms: [],
  themes: [],
  globalConfigs: {},
  audioDevices: [],
  videoDevices: [],
  currentDevices: { video: null, audio: null },
  percents: 0
};

const parseGlobalConfigs = (configs) => {
  const result = {};
  configs.forEach(config => {
    if (config._id === 'optimized_layout_full_quality') {
      if (isNaN(config.value) || parseInt(config.value) === 0) {
        result[config._id] = null;
      } else {
        result[config._id] = parseInt(config.value);
      }
    } else if (config._id === 'optimized_layout_threshold') {
      if (isNaN(config.value)) {
        result[config._id] = null;
      } else {
        result[config._id] = parseInt(config.value);
      }
    } else {
      result[config._id] = config.value;
    }
  });
  if (!result['optimized_layout_full_quality']) {
    result['optimized_layout_full_quality'] = lectureLayouts['default'].geometry.optimized_layout_full_quality;
  }
  if (!result['optimized_layout_threshold']) {
    result['optimized_layout_threshold'] = lectureLayouts['default'].geometry.optimized_layout_threshold;
  }
  return result;
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case userTypes.LOGOUT:
      return Object.assign({}, state, {
        adminSocket: null,
        isAdminPortal: false,
        users: [],
        servers: [],
        liveMeetings: {}
      });
    case types.SET_MEDIA_DEVICES:
      return Object.assign({}, state, {
        audioDevices: payload.audio,
        videoDevices: payload.video
      });
    case types.SET_CURRENT_MEDIA_DEVICES:
      return Object.assign({}, state, { currentDevices: payload });
    case types.SET_ADMIN_PORTAL:
      return Object.assign({}, state, {
        isAdminPortal: payload
      });
    case types.SET_ADMIN_SOCKET:
      return Object.assign({}, state, {
        adminSocket: payload
      });
    case types.SET_GLOBAL_CONFIGS:
      return Object.assign({}, state, {
        globalConfigs: parseGlobalConfigs(payload)
      });
    case types.LICENSE_SUCCESS:
    case types.LICENSE_UPDATE_SUCCESS:
      return Object.assign({}, state, {
        license: payload
      });
    case types.LIVE_MEETINGS_SUCCESS:
      return Object.assign({}, state, {
        liveMeetings: payload
      });
    case types.SERVERS_SUCCESS:
      return Object.assign({}, state, {
        servers: payload
      });
    case types.LICENSE_ERROR:
      return Object.assign({}, state, {
        license: {}
      });
    case types.STORYBOARD_FETCH_SUCCESS:
      return Object.assign({}, state, {
        storyboard: payload
      });
    case types.STORYBOARD_SUCCESS_ALL:
      return Object.assign({}, state, {
        storyboards: payload
      });
    case types.STORYBOARD_LOAD_PROGRESS:
      return Object.assign({}, state, {
        percents: payload
      });
    case types.STORYBOARD_THEMES_SUCCESS:
      return Object.assign({}, state, {
        themes: payload
      });
    case types.USERS_SUCCESS_ALL:
      return Object.assign({}, state, {
        users: payload
      });
    case types.ROOM_SUCCESS_ALL:
      return Object.assign({}, state, {
        rooms: payload
      });
    case types.MCU_SUCCESS_ALL:
      return Object.assign({}, state, {
        mcus: payload
      });
    case types.MCU_STATS:
      return Object.assign({}, state, {
        mcus: payload
      });
    case types.MS_SUCCESS_ALL:
      return Object.assign({}, state, {
        mediaServers: payload
      });
    case types.KURENTO_SUCCESS_ALL:
      return Object.assign({}, state, {
        kurentoServers: payload
      });
    case types.SERVER_STATUS:
      return Object.assign({}, state, {
        liveMeetings: payload
      });
    case types.PARTICIPANT_STATS:
      let newParticipants = {};
      Object.keys(payload.stats).map(participantId => {
        newParticipants[participantId] = Object.assign({},
          state.liveMeetings[payload.roomId].currentParticipants[participantId], payload.stats[participantId]);
      });
      return Object.assign({}, update(state, {
        liveMeetings: {
          [payload.roomId]: {
            currentParticipants: { $merge: newParticipants }
          }
        }
      }));
    case types.GET_TERMINATED_MEETINGS_SUCCESS:
      return Object.assign({}, state, {
        terminatedMeetings: payload
      });
    default:
      return state;
  }
};
