import { all, takeEvery } from 'redux-saga/effects';
import connectionSaga from './connectionSaga';
import moderationsSaga from './moderationSaga';
import { streamingSaga } from './streamingSaga';

function* debug() {
  // yield takeEvery('*', (action) => { console.log('action > ', action); });
  yield;
}

export default function* saga() {
  yield all([
    debug(),
    connectionSaga(),
    moderationsSaga(),
    streamingSaga()
  ]);
}
