import types from './types';

export const roles = {
  ADMIN: 'admin',
  MODERATOR: 'moderator',
  USER: 'user'
};

const initialState = {
  userToken: localStorage.getItem('authToken') || '',
  serverUrl: '',
  profile: null,
  eventRoom: null
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    case types.LOGOUT:
      localStorage.clear();
      return Object.assign({}, state, {
        userToken: '',
        profile: null
      });
    case types.USER_SET_PROFILE:
      localStorage.setItem('currentTheme', payload.theme);
      return Object.assign({}, state, { profile: payload });
    case types.SET_SERVER_URL:
      return Object.assign({}, state, { serverUrl: payload });
    case types.UPDATE_TOKEN:
      localStorage.setItem('authToken', payload);
      return Object.assign({}, state, { userToken: payload });
    case types.LOGIN_SUCCESS:
      localStorage.setItem('currentTheme', payload.theme);
      localStorage.setItem('authToken', payload.token);
      return Object.assign({}, state, {
        profile: payload.profile,
        userToken: payload.token
      });
    case types.LOGIN_ERROR:
      localStorage.removeItem('authToken');
      return Object.assign({}, state, {
        profile: null,
        userToken: ''
      });

    case types.STORYBOARD_SUCCESS_ALL: {
      return Object.assign({}, state, {
        storyboards: payload
      });
    }

    case types.SET_EVENT_ROOM: {
      return Object.assign({},  state, { eventRoom: payload });
    }
    default:
      return state;
  }
};
